<script>
import LeadDataService from "../../../services/DataService";
export default {
    props: {
        productdata: {
            type: Array,
            required: true
        },
        sourceid: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },        
        modulename: {
            type: String,
            required: true
        }

    },
    data: function () {
        return {
            productData: [],
            noRecordForTab: true,
            LeadStatus: '',
            ContactsHeader: [],
            productsearch: '',
            SearchLength: 0,
            OldPrice: 0,
            editUrl: `/CRM/ManageProduct/`,
        }
    },
    watch: {
        productdata: function (value) {
            var vm=this;
            $.each(value, function (index, item) {
                item.isSetPrice = false
            });
            vm.productData = JSON.parse(JSON.stringify(value));
            vm.noRecordForTab = ((vm.productData != null && this.productData.length > 0) ? false : true);          
            setTimeout(function(){vm.ResponsiveDataTable('table-products-content')}, 500);
        }
    },
    directives: {
        'click-outside': {
            bind() {

                this.event = event => this.vm.$emit(this.expression, event)
                this.el.addEventListener('click', this.stopProp)
                document.body.addEventListener('click', this.event)
            },
            unbind() {

                this.el.removeEventListener('click', this.stopProp)
                document.body.removeEventListener('click', this.event)
            },

            stopProp(event) { event.stopPropagation() }
        }
    },
    methods: {
        SavePrice: function (item) {
            var vm = this;
            var object = {
                account_id: vm.sourceid,
                product_id: item.asset_catalogue_id,
                price: item.unit_price == "" ? 0 : item.unit_price == "." ? 0 : item.unit_price,             
                submodulecode: vm.submodulecode
            }
            vm.OldPrice = item.unit_price == "" ? "0" : item.unit_price == "." ? "0" : parseFloat(item.unit_price);
            var json = JSON.stringify(object);
            LeadDataService.SaveProductPrice(json).then(function (response) {
                if (response.data.msg == "UPDATE") {
                    vm.ShowAlert(vm.$t("Price updated Successfully"), "success", true, vm.$t("Alert"));
                    vm.OldPrice = item.unit_price == "" ? "0" : item.unit_price == "." ? "0" : item.unit_price;
                }
            })
        },
        isNumber: function (evt) {

            evt = (evt) ? evt : window.event;
            var charCode = (evt.which) ? evt.which : evt.keyCode;
            if (((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) || evt.currentTarget.value.length > 15) {
                evt.preventDefault();
            } else {
                return true;
            }
        },
        AllowNumbarwith($event, item) {
            let keyCode = ($event.keyCode ? $event.keyCode : $event.which);
          
            if ((keyCode < 48 || keyCode > 57) && (keyCode !== 46 || $event.currentTarget.value.indexOf('.') != -1) || $event.currentTarget.value.length > 15) { 
                $event.preventDefault();
            }
        },
        ToFixed: function (item) {
            if (item.unit_price.toString().indexOf('.') > -1) {
                if (item.unit_price.toString().split('.')[1].length > 1)
                    item.unit_price = parseFloat(item.unit_price).toFixed(2);
            }
            else
                item.unit_price = parseFloat(item.unit_price) || 0;
        },
        ShowPrice: function (item, ind) {
            var vm = this;
            setTimeout(function () {
                item.isSetPrice = !item.isSetPrice;
                if (item.isSetPrice) {
                    vm.OldPrice = item.unit_price;
                    vm.setFocus(item.asset_catalogue_id);
                }
                else
                    item.unit_price = vm.OldPrice;
            }, 400);
        },
        setFocus: function (id) {
            setTimeout(function () {
                $("#" + id).focus();
            }, 200);
        },        
        Searchdata: function () {
            var data = "";
            if ($.trim(this.productsearch) != "") {
                this.SearchLength = $.trim(this.productsearch).length;
                data = " CONCAT(' ',AC.name,' ', AT.asset_type,' ',AC.item_code) LIKE '% " + this.productsearch + "%'";

            }
            this.$parent.loadData("products", data);
        },
        cancelOrRefresh: function () {
            this.productsearch = "";
            this.$parent.loadData("products");
        },
        OnEnterProduct: function (event) {
            if (event.keyCode == 13) {
                this.Searchdata();
            }
        },
        deassociateProduct: function (value) {
            debugger;
            var currentObj = this
            var vm = this;
            if (value > 0) {
                vm.confirmR(vm.$t("ConfirmDeAssociateProduct"), true, null, function (result) {
                    var postJSON = {
                        ids: value,
                        moduleName: currentObj.modulename,
                        subModuleCode: currentObj.submodulecode,
                        refCode: 'DELETE_VIEW_PRODUCTS',
                        refId: currentObj.sourceid
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    LeadDataService.CommonDeleteData(objectPostString).then(function (response) {                          
                        if (response.data[0].Status == "Success") {
                            vm.ShowAlert(vm.$t("Product has been successfully De-associated."), "success", true, vm.$t("Alert"));
                            vm.$parent.loadData('products');
                        }
                        else {
                            vm.ShowAlert(vm.$t('ErrorDeassociateFailure',[vm.$t('Product')]), "success", true, vm.$t("Alert"));
                        }
                         
                    });
                });
            }
            else {
                alertR(vm.$t("DeletedError")+ vm.$t("Contact"));
            }
        },
        UpdateProduct: function (item) {
            $("#hdnProductId").val(item.asset_catalogue_id);
            $("#divProductModal").dialog("open");
        }
    }
}
</script>
<template>
    <div>
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 searchpanel tablehead search float-left p-0">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 Search-panel float-left py-2 p-0">
                <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
                    <div class="row mt-0">
                        <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pr-1 pl-0">
                            <div class="form-group">
                                <input type="search" v-model="productsearch" v-on:keyup="OnEnterProduct($event)"
                                    class="form-control" placeholder="Search By Product Name or Code or Type" />
                            </div>
                        </div>
                        <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pl-0">
                            <div class="search-btm-btn background-0 text-left form-check-inline">
                                <a title="" v-on:click="Searchdata()" href="javascript:;"
                                    class="btn btn-success mr-1 px-4" data-toggle-tooltip="tooltip"
                                    data-original-title="Search"><em class="fa fa-search"></em></a>
                                <a href="javascript:;" title="" v-on:click="cancelOrRefresh()"
                                    class="btn btn-danger clearsearchtext px-4" data-toggle-tooltip="tooltip"
                                    data-original-title="Clear"><em class="fa fa-refresh"></em></a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="listing">
            <div class="table-responsive table-fix-header">
                <table class="table table-bordered mb-0" id="table-products-content" aria-describedby="accountproduct"> 
                    <thead class="thead-bg">
                        <tr>
                             <th scope="col" class="nonresize"><span> {{$t("ItemName")}}  </span></th>
                             <th scope="col"><span> {{$t("ItemCode")}}  </span></th>
                             <th scope="col"><span> {{$t("ItemType")}}  </span></th>
                             <th scope="col"><span> {{$t("AssociatedAt")}}  </span></th>
                             <th scope="col" class="nonresize"><span>UOM</span></th>
                             <th scope="col"><span> {{$t("Price") }} </span></th>
                             <th scope="col"><span> {{$t("Description")}}  </span></th>
                             <th scope="col" class="text-center"><span>{{ $t("Action")}}  </span></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-if="noRecordForTab">
                            <td colspan="9" class="text-danger text-center no-record">
                                {{$t("NoRecordfound")}} </td>
                        </tr>
                        <tr v-for="(item,index) in productData" :key="index">
                            <td v-bind:class="{'text-left':true}">
                                <RouterLink :to="editUrl+item.asset_catalogue_id+'/'+sourceid" class="actions-onclick" ><span
                                        class="text" style="color:dodgerblue;cursor: pointer;"
                                        v-bind:title="item.name">{{item.name}}</span>
                                </RouterLink>
                            </td>
                            <td>{{item.item_code}}</td>
                            <td>{{item.asset_type}}</td>
                            <td>{{item.created_at | formatDate}}</td>
                            <td>{{item.UOM}}</td>
                            <td class="position-relative single-action">
                                <div v-show="(!item.isSetPrice)">{{item.unit_price}}<a class="float-right"
                                        :title="$t('Edit')" v-on:click="ShowPrice(item,index)"><em class="fa fa-pencil text-success action-icon pr-2 ml-3"></em></a></div>
        <div v-show="item.isSetPrice" class="row"> 
        <div class="col-lg-8 col-xl-8 col-md-8 p-0" >
        <input type="text" class="form-control ml-2"  v-bind:id="item.asset_catalogue_id" v-on:keypress="AllowNumbarwith($event,item)" v-on:keyup="ToFixed(item)" maxlength="12" v-model="item.unit_price" v-on:blur="ShowPrice(item,index)"/>
        </div> <div  class="col-lg-4 col-xl-4 col-md-4">
        <a href="javascript:;" :title="$t('Save')" ><em class=" fa fa-save action-icon text-success mr-2 mt-2" v-on:click="SavePrice(item)"></em></a>
        </div>
            </div>
            </td>
            <td>{{item.description}}</td>
            <td class="text-center single-action mobileactions"><a href="javascript:;" v-on:click="deassociateProduct(item.asset_catalogue_id)" 
            :title="$t('DeassociateProduct')"><em class="fa fa-times action-icon text-danger"></em></a>
            </td>       
        </tr>
        </tbody>
        </table>
        </div>
        </div>
        </div>
</template>