<template>
<div class="modal d-block my-popups dialog">
        <loader :is-visible="isLoading"></loader>
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <slot name="header">
                        <h5 class="modal-title">{{contactHeader}}</h5>
                    </slot>
                    <button v-on:click="$emit('close')" type="button" class="close" data-original-title="Close"><em 
                            class="fa fa-times"></em></button>
                </div>
                <div class="modal-body">
                    <div class="row form-head" style="display:none;">
                        <div class="col-12">
                            <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-right p-0">
                                <div class="userguide-box userguide_curve">
                                    <a data-toggle="collapse" href="#user-guidepriority" aria-expanded="false"
                                        aria-controls="search-content" class="userguide-btn collapsed"><em 
                                            class="fa"></em><span>{{ $t("GuideForUser")}} </span></a>
                                    <div class="clearfix"></div>
                                    <div id="user-guidepriority" class="user-guide collapse">Fill the Form to add
                                        contact. Some fields are not mendatory, but please fill these fields for more
                                        information for in future when needed.<br><br></div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <Form  v-slot="{valid,errors}" ref="form"> 
                        <div class="d-flex flex-wrap">
                            <div class="col-md-12 p-0">
                            <div class="row">
                                <div class="col-lg-12 mt-1 mb-1">
                                    <div class="heading-border">
                                        <h5 class="h5"><span>{{$t("ContactInformation")}}</span></h5>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group">
                                        <label>{{$t("FirstName")}}:<span class="text-danger">*</span>
                                        </label>
                                        <Field name="First Name" :rules="{'required':true}" 
                                            v-slot="{errors}">
                                            <input
                                                v-bind:class="{'form-control' : true,'is-invalid': errors[0]}"
                                                id="txtFirstName" name="txtFirstName" maxlength="100"
                                                v-model="contactData.txtFirstName" type="text" >
                                            <span  v-if="errors[0]" name="First Name"
                                                class="invalid-feedback d-block">{{errors[0]}}</span>
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group"><label> {{$t("LastName")}}:<span
                                                class="text-danger">*</span></label>
                                        <Field name="Last Name" :rules="{'required':true}" 
                                           v-slot="{errors}">
                                            <input
                                                v-bind:class="{'form-control' : true,'is-invalid': errors[0]}"
                                                id="txtLastName" name="txtLastName" maxlength="100"
                                                v-model="contactData.txtLastName" type="text">
                                            <span  v-if="errors[0]" name="Last Name"
                                                class="invalid-feedback d-block">{{errors[0]}}</span>
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left"
                                    v-if="submodulecode=='CRM_CAMPAIGNS'">
                                    <div class="form-group"><label> {{$t("JobTitle")}}<span
                                                class="text-danger">*</span></label>
                                        <Field name="txtJobTitle" :rules="{'required':true}" 
                                           v-slot="{errors}">
                                            <input
                                                v-v-bind:class="{'form-control' : true,'is-invalid': errors[0]}"
                                                id="txtJobTitle" name="txtJobTitle" maxlength="100"
                                                v-model="contactData.txtJobTitle" type="text"/>
                                            <span  v-if="errors[0]" name="txtJobTitle"
                                                class="invalid-feedback d-block">{{errors[0]}}</span>
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group"><label> {{$t("Role")}} {{$t("Title") }}:</label>
                                        <div class="input-group">
                                            <Field id="RoleType" name="RoleType" :rules="{'required':false}"  v-slot="{errors}">                                                                                                  
 <select class="form-control"  v-model="contactData.ddlroleTitle">
    <option value="">Select</option>
                                                <option v-for="item in RoleTitleDDLData" v-bind:value="item.value">
                                                    {{item.name}}</option> 

                    </select>
                    
<span  v-if="errors[0]" name="RoleType" class="invalid-feedback d-block">{{errors[0]}}</span>  
                                            </Field>
                                            <div class="input-group-append" style="cursor:pointer;"><span
                                                    class="input-group-text border-0 bg-transparent p-0 pl-2"><a href="javascript:;"
                                                        class="round-icon-small btn-dark theme-primary" v-on:click="openRoletitleModal"><em 
                                                            class="fa fa-plus pt-7"></em></a></span></div>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group"><label> {{$t("EmailId") }}:<span
                                                class="text-danger">*</span></label>
                                        <Field name="Email" :rules="{'required':true,'email':true}" 
                                           v-slot="{errors}">
                                            <input
                                                v-bind:class="{'form-control' : true,'is-invalid': errors[0]}"
                                                id="txtEmailId" name="txtEmailId" v-model="contactData.txtEmailId"
                                                type="text">
                                            <span v-if="errors[0]"  name="Email"
                                                class="invalid-feedback d-block">{{errors[0]}}</span>
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group"><label> {{$t("Mobile")}}:</label>

                                        <Field name="txtMobilePhone" :rules="{'required':false}" 
                                            v-slot="{errors}">
                                            <vue-tel-input id="txtMobilePhone" v-model="contactData.txtMobilePhone" 
                                                name="txtMobilePhone" :autoDefaultCountry="false" mode="international"
                                                @input="onInputMobilePhone">
                                            </vue-tel-input>
                                            <span  v-if="errors[0]" name="txtMobilePhone" class="invalid-feedback d-block">{{errors[0]}}</span>
                                            <span id="spantxtMobilePhone" v-if="mobilephoneError" class="invalid-feedback d-block">Enter valid {{ $t("MOBILE_PHONE") }}.</span>
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group"><label> {{$t("Home")}} {{$t("Phone") }}:</label>
                                        <Field name="txtHomePhone" :rules="{'required':false}" 
                                            v-slot="{errors}">
                                            <vue-tel-input id="txtHomePhone" v-model="contactData.txtHomePhone" 
                                                name="txtHomePhone" :autoDefaultCountry="false" mode="international" 
                                                @input="onInputHomePhone">
                                            </vue-tel-input>
                                            <span  v-if="errors[0]" name="txtHomePhone"
                                                class="invalid-feedback d-block">{{errors[0]}}</span>
                                            <span id="spantxtHomePhone" v-if="homephoneError" class="invalid-feedback d-block">Enter valid {{$t("Home")}} {{$t("Phone")}}.</span>
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                <div class="form-group"><label> {{$t("BusinessPhone") }}:</label>
                                    <Field name="txtBusinessPhone" :rules="{'required':false}"
                                        v-slot="{errors}">
                                        <vue-tel-input id="txtBusinessPhone" @input="onInputBusinessPhone"
                                            v-model="contactData.txtBusinessPhone" name="txtBusinessPhone" :autoDefaultCountry="false"
                                            mode="international" v-on:keypress="isNumber($event)">
                                        </vue-tel-input>
                                        <span v-if="errors[0]"  name="txtBusinessPhone"
                                            class="invalid-feedback d-block">{{errors[0]}}</span>
                                    <span id="spantxtBusinessPhone" v-if="businessphoneError" class="invalid-feedback d-block">Enter valid {{ $t("BusinessPhone") }}.</span>
                                    </Field>
                                </div>  
                                </div>                                                                                         
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left"
                                    v-if="submodulecode=='CRM_CAMPAIGNS'">
                                    <div class="form-group"><label> {{$t("Fax")}}:</label>
                                        <Field name="txtFax" :rules="{'required':false}" 
                                            v-slot="{errors}">
                                            <input class="form-control" id="txtFax" name="Fax"
                                                v-on:keypress="isNumber($event)" maxlength="15"
                                                v-model="contactData.txtFax" type="text">
                                            <span  v-if="errors[0]" name="txtFax"
                                                class="invalid-feedback d-block">{{errors[0]}}</span>
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left"
                                    v-if="submodulecode=='CRM_CAMPAIGNS'">
                                    <div class="form-group"><label> {{$t("PreferredContact") }}:<span
                                                class="text-danger">*</span></label>
                                        <Field name="txtPreferredContact" :rules="{'required':false}" 
                                           v-slot="{errors}">
                                            <input
                                                v-bind:class="{'form-control' : true,'is-invalid': errors[0]}"
                                                id="txtPreferredContact" name="txtPreferredContact"
                                                v-model="contactData.txtPreferredContact" type="text"
                                                v-bind:maxlength="maxLength" /><span  v-if="errors[0]"
                                                name="txtPreferredContact"
                                                class="invalid-feedback d-block">{{errors[0]}}</span>
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left"
                                    v-if="submodulecode=='CRM_CAMPAIGNS'">
                                    <div class="form-group"><label> {{$t("AddressStreet3")}}:</label>
                                        <Field name="txtAddressStreet3" :rules="{'required':false}" 
                                           v-slot="{errors}">
                                            <input class="form-control" id="txtAddressStreet3"
                                                v-bind:class="{'form-control' : false,'is-invalid': errors[0]}"
                                                name="txtAddressStreet3" maxlength="200"
                                                v-model="contactData.txtAddressStreet3" type="text">
                                            <span  v-if="errors[0]" name="txtAddressStreet3"
                                                class="invalid-feedback d-block">{{errors[0]}}</span>

                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group"><label> {{$t("DateOfBirth")}}:</label>
                                        <div class="input-group">
                                            <Field name="txtDateOfBirth" :rules="{ 'required': false }"
                                              
                                                v-slot="{ errors, changed }">
                                                <v-date-picker 
                                                    :max-date="getMinDate()" :minute-increment="5"
                                                    class="input-group" v-model="contactData.txtDateOfBirth" @update:modelValue="changed">                                            
                                                    <template v-slot="{ inputValue, inputEvents, togglePopover }">                                            
                                                        <input v-bind:class="{'form-control' : true,
                                                        'is-invalid': errors[0]}" :value="inputValue"
                                                            v-on="inputEvents"/>
                                                        <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em 
                                                                    class="fa fa-calendar"></em></span></div>
                                                    </template>
                                                </v-date-picker>
                                                <span v-if="errors[0]"  name="txtDateOfBirth" class="invalid-feedback d-block">{{ errors[0]
                                                    }}</span>
                                            </Field>                                          
                                            
                                        </div>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group">
                                        <label> {{$t("Gender")}} <span class="text-danger"></span></label>
                                        <Field id="Gender" name="Gender" :rules="{'required':false}">
                                            <select class="form-control"  v-model="contactData.rGender">
                                            <option value="">Select</option>
                                            <option value="Male">Male</option>
                                            <option value="Female">Female</option>
                                            <option value="Other">Other</option>
                                            </select>                                           
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left"
                                    v-if="submodulecode=='CRM_CAMPAIGNS'">
                                    <div class="form-group"><label> {{$t("MaritalStatus") }}:</label>
                                        <select
                                            class="form-control" v-model="contactData.maritalStatus">
                                            <option value="">Select</option>
                                            <option value="Single">Single</option>
                                            <option value="Married">Married</option>
                                            <option value="Divorced">Divorced</option>
                                        </select></div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left"
                                    v-if="submodulecode=='CRM_CAMPAIGNS'">
                                    <div class="form-group"><label> {{$t("SpouseName")}} :</label>
                                        <input
                                            class="form-control" id="txtSpouseName" name="txtSpouseName" maxlength="100"
                                            v-model="contactData.txtSpouseName" type="text"></div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left"
                                    v-if="submodulecode=='CRM_CAMPAIGNS'">
                                    <div class="form-group"><label> {{$t("GroupName")}} :</label>
                                        <select
                                            class="form-control" v-model="contactData.txtGroupName">
                                            <option value="">Select Group Name</option>
                                            <option v-for="item in groupDDLData" v-bind:value="item.value">{{item.name}}
                                            </option>
                                        </select>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left"
                                    v-if="submodulecode=='CRM_CAMPAIGNS'">
                                    <div class="form-group"><label> {{$t("PersonalNotes")}} :</label><textarea
                                            class="form-control" id="txtPersonalNotes" name="txtPersonalNotes"
                                            v-model="contactData.txtPersonalNotes"></textarea></div>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-lg-12 mt-1 mb-1">
                                    <div class="heading-border">
                                        <h5 class="h5"><span> {{$t("AddressInformation")}}</span></h5>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group"><label> {{$t("AddressStreet1")}} :</label><input
                                            v-bind:class="{'form-control':true}" id="txtAddressLine1"
                                            name="txtAddressLine1" v-model.trim="contactData.txtAddressLine1"
                                            type="text" />
                                            </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group"><label> {{$t("AddressStreet2")}} :</label><input
                                            class="form-control" id="txtAddressLine2" name="txtAddressLine2"
                                            maxlength="200" v-model.trim="contactData.txtAddressLine2" type="text">
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group"><label> {{$t("City") }}:</label><input class="form-control"
                                            id="txtCity" name="txtCity" maxlength="200"
                                            v-model.trim="contactData.txtCity" type="text" /></div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group"><label> {{$t("State")}} :</label><select
                                            class="form-control" v-model="contactData.txtState">
                                            <option value="">Select</option>
                                            <option v-for="item in stateDDLData" v-bind:value="item.value">{{item.name}}
                                            </option>
                                        </select></div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group"><label> {{$t("Country")}} :</label><select
                                            class="form-control" v-model="contactData.country"
                                            v-on:change="FillDropdown(contactData.country)">
                                            <option v-for="item in countryDDLData" v-bind:value="item.value">
                                                {{item.name}}</option>
                                        </select></div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group"><label> {{$t("ZipCode")}} :</label>
                                        <Field name="ZipCode" :rules="getZipCodeValidation()"  v-slot="{errors}">
                                            <input type="text" class="form-control" id="txtZipCode" name="ZipCode" maxlength="15"
                                                v-model="contactData.txtZipCode"  onpaste="return false;">
                                                <span v-if="errors[0]"  name="ZipCode"
                                                class="invalid-feedback d-block">{{errors[0]}}</span>
                                        </Field>
                                    </div>
                                </div>

                            </div>
                            <div class="row">
                                <div class="col-lg-12 mt-1 mb-1">
                                    <div class="heading-border">
                                        <h5 class="h5"><span>System Information</span></h5>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group"><label> {{$t("Status")}} :<span
                                                class="text-danger">*</span></label>
                                                <Field name="Status" :rules="{'required':true}" v-slot="{errors}">
                                                    <select v-model="contactData.Status" v-bind:class="{'form-control':true, 'is-invalid':errors[0]}"
                                                         id="Status" name="Status" v-on:change="getSelectedValue" >
                                                        <option value="">Select</option>
                                                        <option v-for="(item,itemIndex) in statusDDLData" :key="itemIndex" v-bind:value="item.value">
                                                            {{item.name}}</option>
                                                    </select>
                                                    <span  v-if="errors[0]" name="Status" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left mt-2">
                                    <label class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">Is Primary:</label>
                                    <div class="form-group text-center float-left">
                                        <label class="switch">
                                            <input class="form-check-input custom-control-input dynamic" type="checkbox"
                                                v-on:change="SetPrimary(contactData.IsPrimary)"
                                                v-model="contactData.IsPrimary">
                                            <span class="slider round">
                                                <span class="slider-yes">Yes</span>
                                                <span class="slider-no">No</span>
                                            </span>
                                        </label>
                                    </div>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-lg-12 mt-1 mb-1">
                                    <div class="heading-border">
                                        <h5 class="h5"><span> {{$t("Additionalinformation")}}</span></h5>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                    <div class="form-group">
                                        <label> {{$t("Description")}}:</label>
                                        <textarea
                                            class="form-control" id="txtPersonalNotes" name="txtPersonalNotes"
                                            maxlength="1000" v-model="contactData.txtPersonalNotes"
                                            style="max-height:100px!important;min-height:100px!important"></textarea>
                                    </div>
                                </div>
                            </div>
                            <div class="row flex-row-reverse mt-2">
                                <div class="col-lg-6 text-right">
                                    <a href="javascript:;" class="btn btn-success mr-1" v-on:click="SaveContact(valid,errors)"><em 
                                            class="fa fa-save pr-2"></em> {{$t("Submit")}} </a>
                                    <a href="javascript:;" v-on:click="Close" class="btn btn-danger"><em 
                                            class="fa fa-times"></em> {{$t("Cancel")}}</a>
                                </div>
                                <div class="col-lg-6">
                                    <div class="py-1">
                                        <small class="text-danger"> {{$t("MandatoryString") }}</small>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>               
                    </Form>                  
                    <modaladdroletitle v-if="IsContactRole" @close="IsContactRole=false" v-bind:sourceid="sourceid"
                    v-bind:submodulecode="submodulecode" v-bind:modulename="modulename" v-bind:companyid="companyid"
                            @updatedata="UpdateSelectJson"></modaladdroletitle>
                </div>
            </div>
        </div>
    </div>

</template>

<script>
import $ from "jquery";
import DataService from "../../../services/DataService";
import modaladdroletitle from '../../../components/Common/Modal/modaladdroletitle.vue'
export default {
    components: {
    modaladdroletitle
  },
    props: {
        sourceid: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
        userinfo: {
            type: String,
            required: true
        },
        companyid: {
            type: String,
            required: true
        },
        DateFormat:null,
        modulename: {
            type: String,
            required: true
        },
        sourcename: {
            type: String,
            required: true
        },
        iscontactDetails: null
    },
    data: function () {
        return {
            isLoading: false,
            options: {
                format: null,
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
            },
            datePickerMaxConfig: {
                format:null,
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                maxDate: new Date(Date.now())
            },
            countryDDLData: [],
            stateDDLData: [],
            contactHeader: '',
            maxLength: 20,
            statusDDLData: [],
            groupDDLData: [],
            RoleTitleDDLData: [],
            IsContactRole: false,
            contactData: {
                contactId: null,
                txtFirstName: null,
                txtLastName: null,
                txtJobTitle: null,
                ddlroleTitle: '',
                txtEmailId: null,
                txtBusinessPhone: null,
                txtMobilePhone: null,
                maritalStatus: '',
                txtGroupName: '',
                txtFax: null,
                txtPreferredContact: null,
                txtAddressLine1: null,
                txtAddressLine2: null,
                txtAddressStreet3: null,
                txtCity: null,
                txtState: '',
                txtZipCode: null,
                txtHomePhone: null,
                IsPrimary: false,
                country: '',
                rGender: '',
                txtSpouseName: null,
                txtDateOfBirth: '',
                txtPersonalNotes: null,
                Status: '',
                type: '',
                userId: null,
                companyId: null,
                sourceId: null,
                moduleName: null,
                subModuleCode: null,
                groupID: null,                
            },
            phoneList: [],
            businessphoneError: false,
            homephoneError: false,
            mobilephoneError: false,
            statusValue:''
        }
    },
    created: function () {
        var vm = this;       
        if (vm.iscontactDetails != null && vm.iscontactDetails != '') {
            vm.contactHeader = vm.$t('EditContact');
        } else {
            vm.contactHeader = vm.$t('AddContact');
        }
        setTimeout(function(){vm.RadioButtonBootstrap();}, 500);
        vm.GetCountryDDLValues();
        vm.GetStatusDDLData();
        vm.fetchContactForForm();
        vm.GetGroupNameDDLData();
        vm.GetRoleTitleData();       
        setTimeout(function () {
            if (vm.contactData.contactId == null || $.trim(vm.contactData.txtBusinessPhone) == "" || $.trim(vm.contactData.txtHomePhone) == "" || $.trim(vm.contactData.txtMobilePhone) == "") {
                var Phones = [];
                if ($.trim(vm.contactData.txtBusinessPhone) == "")
                    Phones.push("txtBusinessPhone");
                if ($.trim(vm.contactData.txtHomePhone) == "")
                    Phones.push("txtHomePhone");
                if ($.trim(vm.contactData.txtMobilePhone) == "")
                    Phones.push("txtMobilePhone");
                $.each(Phones, function (ind, phn) {
                    $.each(vm.$children, function (indx, elements) {
                        if (elements.$children.length > 0) {
                            $.each(elements.$children, function (index, element) {
                                if (element.$el._prevClass.indexOf("vue-phone-number-input") > -1 && element.$el.id == phn)
                                    element.countryCode = 'US';
                            });
                        }
                    });
                });
                
            }
        }, 500);
        setTimeout(function () {
            vm.FillDropdown(vm.contactData.country, 'EDIT');
        }, 1000)
    },
    methods: {
        getZipCodeValidation(){
            var initialRule={}
                initialRule.required=false;
                initialRule.regex=/^[a-zA-Z0-9 ]*$/;
                initialRule.min=[5];
                initialRule.max=[15];
                return initialRule;
        },
        SetPrimary: function (IsPrimary) {
            var vm = this;
            var message = "Are you sure you want to change Primary?";
            vm.confirmR(message, true, null, function (result) {               
                        vm.contactData.IsPrimary = IsPrimary;
                    
                }
            );
        },
        getSelectedValue: function (event) {
            debugger
                this.statusValue = event.target.value;
            },
        SaveContact: function (e) {
            debugger
            var vm = this;
        //     this.$refs.form.setFieldTouched('txtFirstName', true);  
        //   this.$refs.form.setFieldTouched('txtLastName', true); 
        //    this.$refs.form.setFieldTouched('txtEmailId', true); 
        //    this.$refs.form.setFieldTouched('Status', true); 
            var checkValue = null;
            checkValue = vm.iscontactDetails;
            vm.$refs.form.validate().then((result) => {
                if (result) {
                    vm.isLoading=true;
                    vm.maritalStatus = ((vm.maritalStatus != '') ? vm.maritalStatus : null);
                    vm.country = ((vm.country != '') ? vm.country : null);
                    vm.contactData.sourceId = vm.sourceid;
                    vm.contactData.moduleName = vm.modulename;
                    vm.contactData.subModuleCode = vm.submodulecode;
                    vm.contactData.groupID = vm.txtGroupName;
                    vm.contactData.type = vm.sourcename;      
                    vm.contactData.Status=vm.statusValue;                                   
                    var objectPostString = JSON.stringify(vm.contactData);
                    DataService.SaveContactWithMapping(objectPostString).then(function (response) {                       
                        if (response.data[0].Status == "Success") {
                            vm.$emit('close');
                           
                            if (checkValue != null) {
                                vm.ShowAlert("Contact Updated Successfully", "success", true, vm.$t("Alert"));
                            }
                            else {
                                vm.ShowAlert("Contact Created Successfully", "success", true, vm.$t("Alert"));
                                vm.$parent.openCRMExistingContactModel();
                            }
                        }
                        else if (response.data[0].Status.toUpperCase() == "EXIST") {
                            vm.ShowAlert("Email id already exist.", "warning", true, vm.$t("Alert"));
                        }
                        vm.isLoading=false;
                    });
                }else{
            console.log(this.$refs.form.errors);
        }
            });
        },
        formatDate: function (value) {
            if (value) {
                return moment(String(value), "YYYY-MM-DD").format(String(this.DateFormat==''?'MM/DD/YYYY':this.DateFormat).toUpperCase())
            }
        },
        isNumber: function (evt) {
         var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                evt.preventDefault();
            } else {
                return true;
            }
        },
 
        fetchContactForForm: function () {
            var vm = this;
            var checkValue = vm.iscontactDetails;
            if (checkValue != null && checkValue != 0) {
                var url = "id=" + checkValue;
               DataService.GetSingleContactDetails(url).then(function (response) {
                        vm.isLoading=false;
                    if (response.data != null) {
                        vm.contactData.contactId = response.data.CONTACT_ID;
                        vm.contactData.txtFirstName = response.data.FIRST_NAME;
                        vm.contactData.txtLastName = response.data.LAST_NAME;
                        vm.contactData.txtJobTitle = response.data.JOB_TITLE;
                        vm.contactData.txtEmailId = response.data.EMAIL_ID;
                        vm.contactData.txtBusinessPhone = response.data.BUSINESS_PHONE?response.data.BUSINESS_PHONE:'';
                        vm.contactData.txtHomePhone = response.data.HOME_PHONE?response.data.HOME_PHONE:'';
                        vm.contactData.txtMobilePhone = response.data.MOBILE_PHONE?response.data.MOBILE_PHONE:'';
                        vm.contactData.maritalStatus = ((response.data.MARITAL_STATUS != null) ? response.data.MARITAL_STATUS : '');
                        vm.contactData.txtFax = response.data.FAX;
                        vm.contactData.txtPreferredContact = response.data.PREFERRED_CONTACT;
                        vm.contactData.txtAddressLine1 = response.data.ADDRESS_STREET_1;
                        vm.contactData.txtAddressLine2 = response.data.ADDRESS_STREET_2;
                        vm.contactData.txtAddressStreet3 = response.data.ADDRESS_STREET_3;
                        vm.contactData.txtCity = response.data.CITY;
                        vm.contactData.txtState = response.data.STATE;
                        vm.contactData.txtZipCode = response.data.ZIPCODE;
                        vm.contactData.country = ((response.data.COUNTRY_ID == null || response.data.COUNTRY_ID == 0) ? '' : response.data.COUNTRY_ID);
                        vm.contactData.rGender = ((response.data.GENDER != null) ? response.data.GENDER : '');
                        vm.contactData.txtSpouseName = response.data.SPOUSE_NAME;
                        vm.contactData.txtDateOfBirth = response.data.DATE_OF_BIRTH;
                        vm.contactData.txtPersonalNotes = response.data.PERSONAL_NOTES;
                        vm.contactData.Status =String(response.data.STATUS_ID);
                        vm.contactData.type = vm.sourcename;
                        vm.contactData.txtGroupName = ((response.data.GROUP_ID != null) ? response.data.GROUP_ID : '');
                        vm.contactData.IsPrimary = response.data.IS_PRIMARY;
                        vm.contactData.ddlroleTitle = ((response.data.ROLE_TITLE_ID == null || response.data.ROLE_TITLE_ID == 0) ? '' : response.data.ROLE_TITLE_ID);
                   
                        var frmFields = {}
                        frmFields['contactId'] = vm.contactData.contactId;
                        frmFields['txtFirstName'] = vm.contactData.txtFirstName;
                        frmFields['txtLastName'] = vm.contactData.txtLastName;
                        frmFields['txtJobTitle'] = vm.contactData.txtJobTitle;
                        frmFields['txtEmailId'] = vm.contactData.txtEmailId;
                        frmFields['txtBusinessPhone'] = vm.contactData.txtBusinessPhone;
                        frmFields['txtHomePhone'] = vm.contactData.txtHomePhone;
                        frmFields['txtMobilePhone'] = vm.contactData.txtMobilePhone;
                        frmFields['maritalStatus'] = vm.contactData.maritalStatus;
                        frmFields['txtFax'] = vm.contactData.txtFax;
                        frmFields['txtPreferredContact'] = vm.contactData.txtPreferredContact;
                        frmFields['txtAddressLine1'] = vm.contactData.txtAddressLine1;
                        frmFields['txtAddressLine2'] = vm.contactData.txtAddressLine2;
                        frmFields['txtAddressStreet3'] = vm.contactData.txtAddressStreet3;
                        frmFields['txtCity'] = vm.contactData.txtCity;
                        frmFields['txtState'] = vm.contactData.txtState;
                        frmFields['txtZipCode'] = vm.contactData.txtZipCode;
                        frmFields['country'] = vm.contactData.country;
                        frmFields['rGender'] = vm.contactData.rGender;
                        frmFields['txtSpouseName'] = vm.contactData.txtSpouseName;
                        frmFields['txtDateOfBirth'] = vm.contactData.txtDateOfBirth;
                        frmFields['txtPersonalNotes'] = vm.contactData.txtPersonalNotes;
                        frmFields['Status'] = vm.contactData.Status;
                        frmFields['type'] = vm.contactData.type;
                        frmFields['txtGroupName'] = vm.contactData.txtGroupName;
                        vm.$refs.form.resetForm({ values: frmFields });
                    }
                   
                });
            }
        },
        GetCountryDDLValues: function () {
            var vm = this;
            var url = "id=&moduleName=" + vm.modulename + "&type=COUNTRIES";
            vm.isLoading=true;
            DataService.getDDLData(url)
                .then(function (response) {
                    vm.isLoading=false;
                    if (response.data != null) {
                        vm.countryDDLData = response.data.DATA;
                    }
                    var cntrydata = vm.countryDDLData;
                    if (vm.contactData.contactId == 0 || vm.contactData.contactId == null) {
                        $.each(cntrydata, function (index, item) {
                            if (item.name == "United States")
                                vm.contactData.country = item.value;
                        });
                    }    
                    vm.isLoading=false;
                });
        },
        FillDropdown: function (value, type) {
            var vm = this;
            var refId = value;
            var typeCode = 'ACCOUNT_STATE';
            var url =  "id=&moduleName=CRM&type=" + typeCode + "&refId=" + refId;
            DataService.getDDLData(url).then(function (response) {
                var json = [];
                if (response.data != null) {
                    json = response.data.DATA;
                    vm.stateDDLData = json;
                    if (type != 'EDIT') {
                        vm.contactData.txtState = '';
                    }
                }
            });
        },

