            this.RoleTitleDDLData =json;
        },
        onInputMobilePhone(phone, phoneObject, input) {
            if(phone){
            if(phoneObject && phoneObject?.formatted.length >0 && (!phoneObject?.valid || phoneObject?.valid==undefined)){
                this.mobilephoneError = true;
            } else
                if (phoneObject?.formatted && phoneObject?.valid) {
                    this.contactData.txtMobilePhone = phoneObject.formatted;
                    this.mobilephoneError = false;
                }
            }
        },
        onInputHomePhone(phone, phoneObject, input) {
            if(phone){
            if(phoneObject && phoneObject?.formatted.length >0 && (!phoneObject?.valid || phoneObject?.valid==undefined)){
                this.homephoneError = true;
            } else
            if (phoneObject?.formatted && phoneObject?.valid) {
                this.contactData.txtHomePhone = phoneObject.formatted;
                this.homephoneError = false;
            }
        }
        },
        onInputBusinessPhone(phone, phoneObject, input) {
            if(phone){
            if(phoneObject && phoneObject?.formatted.length >0 && (!phoneObject?.valid || phoneObject?.valid==undefined)){
                this.businessphoneError = true;
            } else
            if (phoneObject?.formatted && phoneObject?.valid) {
                this.contactData.txtBusinessPhone = phoneObject.formatted;
                this.businessphoneError = false;
            }
        }
        },  
        onBlur(ths) {
            var value=$(`div[name="txtDateOfBirth"]`).attr('value');
            if (value) {
                $(ths.target).removeClass('is-invalid');
                $(ths.target).parent().next().addClass('d-none');
            } else {
                $(ths.target).addClass('is-invalid');
                if ($(ths.target).parent().next().is('span')) {
                    $(ths.target).parent().next().removeClass('d-none');
                } else {
                    $(ths.target).parent().parent().append(`<span name="${this.$t('txtDateOfBirth')}" class="invalid-feedback d-block">${this.$t('DateOfBirth')} is required.</span>`);
                }
            }
        }
    },
}
</script>



