 <script>
import LeadDataService from "../../../services/DataService";
import crmtimeline from "../../../components/Common/Modal/crmtimeline.vue";
import crmnote from "../../../components/Common/Modal/crmnote.vue";
import crmattachment from "../../../components/Common/Modal/crmattachment.vue";
import modaltask from "../../../components/Common/Modal/modaltask.vue";
import modalattachment from "../../../components/Common/Modal/modalattachment.vue";
import modalevent from "../../../components/Common/Modal/modalevent.vue";
import modalcall from "../../../components/Common/Modal/modalcall.vue";
import modalproduct from "../../../components/Common/Modal/modalproduct.vue";
import modalexistingcontact from "../../../components/Common/Modal/modalexistingcontact.vue";
import AccountModalContact from "../../../components/Common/Modal/AccountModalContact.vue";
import modaladdaddress from '../../../components/Common/Modal/modaladdaddress.vue'
import accountcontacttab from '../Accounts/accountcontacttab.vue'
import accountproduct from '../Accounts/accountproduct.vue'
export default {
    components: {
        crmtimeline,
        crmnote,
        crmattachment,
        modaltask,
        modalattachment,
        modalevent,
        modalcall,
        modalproduct,
        modalexistingcontact,
        AccountModalContact,
        modaladdaddress,
        accountproduct,
        accountcontacttab
    },
    data() {
        return {
            activeKey: 1,
            iscontactDetails: null,
            IsContact: false,
            IsNotes: false,
            isProduct: false,
            isAttachment: false,
            showExistingContactModal: false,
            showContactModal: false,
            showAddTaskModal: false,
            showAddEventModal: false,
            showProductModal: false,
            showCallModal: false,
            LeftViewData: [],
            RightViewData: null,
            sendEmailModal: false,
            showAttachmentModal: false,
            attachmentData: [],
            activitiesData: null,
            addressData: null,
            timelinedata: [],
            noteData: [],
            productData: [],
            productHeaders: null,
            invoiceHeader: null,
            invoiceOrderData: null,
            dealsData: null,
            dealsHeader: null,
            leadsHeader: null,
            leadsData: [],
            contactsData: [],
            contactsHeader: [],
            isShowNoteDialogue: false,
            noRecordForTab: false,
            EmailData: [],
            modalItem: null,
            modalTaskItem: null,
            modalFollowUpItem: null,
            modalCallItem: null,
            modalDeleteItem: null,
            accountName: '--',
            noteText: '',
            SortBy: '',
            SortExp: '',
            PageNumber: 1,
            PageSize: 50,
           isView: false,
            modal: null,
            HeaderTitle: '',
            IsEdit: false,
            IsAddress: false,
            location: '',
            showAddAddress: false,
            addressdetail: [],
            isShowNotePopup: false,
            userid:null,
            companyId: null,
            DateFormat:null,
            name:null,
            containername: null,
            email: null,
            NoteListType: 'list',
            editUrl: `/CRM/ManageAccount/${this.$route.params.id}/CRM/CRM_CONTACTS`,
            ApiURL: `id=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}`,
            maxsize: "@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(useloggedUserInfo.CompanyId, useloggedUserInfo.ID, 'CRM', 'CRMPerFileSize')",
            disallowedfiletypes: "",
            isLoading:false
        }
    },
    created: function () {
       
        this.userid= this.GetUserInfo.ID;
        this.companyId= this.GetUserInfo.CompanyId;
        this.containername= this.GetUserInfo.ContainerName;
        this.name= this.GetUserInfo.Name;
        this.email= this.GetUserInfo.Email;
        this.DateFormat=this.GetUserInfo.DateFormat;
        this.FetchData();
    },
    methods: {
        openDealView: function (item) {
            var url = '/CRM/ViewDealDetails/'+ item.deal_id + '/CRM/CRM_DEALS';
            window.location.href = url;
        },
        OpenAddress: function () {
            this.showAddAddress = true;
        },
        openAddTaskModel() {
            this.showAddTaskModal = (this.showAddTaskModal) ? false : true;
        },
        openAddNewEvent() {
            this.showAddEventModal = (this.showAddEventModal) ? false : true;
        },
        openCallEvent() {
            this.showCallModal = (this.showCallModal) ? false : true;
        },
        openNoteDialogue() {
            this.isShowNoteDialogue = (this.isShowNoteDialogue) ? false : true;
        },
        openCRMAttachmentModal: function () {
               var vm=this;
            if ((typeof this.checkForAdditionalPermission !== 'undefined') && !this.checkForAdditionalPermission("AddAttachment")) {
                vm.ShowAlert(vm.$t("additionalOwnerAuthorityWarning"), true, vm.$t('Alert'));
            } else {

                this.showAttachmentModal = (this.showAttachmentModal) ? false : true;
            }
        },
        openCRMProductModel: function () {
            if ((typeof this.checkForAdditionalPermission !== 'undefined') && !this.checkForAdditionalPermission("AddProducts")) {
                vm.ShowAlert(vm.$t("additionalOwnerAuthorityWarning"), true, vm.$t('Alert'));
            } else {
                this.showProductModal = (this.showProductModal) ? false : true;
            }
        },
        DisableButton: function () {
            var vm = this;
            vm.IsContact = false;
            vm.IsNotes = false;
        },
        FetchData: function () {
            this.HeaderTitle = "Detail"
            this.IsEdit = true;
             debugger;
            var vm = this;
            vm.isLoading=true;
            LeadDataService.FormLayoutView(vm.ApiURL)
                .then(function (response) {
                    vm.isLoading=false;
                    var fullLeftJSON = response.data.LEFTDATA;
                    fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
                        return element.form_field_visibility == 'YES';
                    });
                    if (response.data.RIGHTDATA != null && response.data.RIGHTDATA.length > 0) {
                        var fullJSON = response.data.RIGHTDATA;
                        var gruopBy = "";
                        $.each(fullJSON, $.proxy(function (index, item) {
                            gruopBy = item.created_date;
                            var chkGroup = $.grep(vm.timelinedata, function (element, index) {
                                return element.groupKey == gruopBy;
                            });
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(fullJSON, function (element, index) {
                                    return element.created_date == gruopBy;
                                });
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        groupKey: gruopBy,
                                        groupData: returnedData
                                    };
                                    vm.timelinedata.push(objGroup);
                                }
                            }
                        }, vm));
                        vm.isLoading=false;
                        vm.noRecordForTab = false;
                    } else {
                        vm.isLoading=false;
                        vm.noRecordForTab = true;
                    }
                    if (fullLeftJSON != null && fullLeftJSON.length > 0) {
                        var groupId = fullLeftJSON[0].group_id;
                        $.each(fullLeftJSON, $.proxy(function (index, item) {
                            if (item.name == 'account_name') {
                                vm.accountName = item.value;
                            }
                            if (item.name == 'account_owner') {
                                if (item.value == "" || item.value == null) {
                                    item.value = vm.name;
                                }
                            }
                            var chkGroup = $.grep(vm.LeftViewData, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(fullLeftJSON, function (element, index) {
                                    return element.group_id == groupId;
                                });
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        group_id: groupId,
                                        group_name: returnedData[0].group_name,
                                        LeftViewData: returnedData
                                    };
                                    vm.LeftViewData.push(objGroup);
                                }
                            }
                            groupId = item.group_id;
                        }, vm));
                    }
                    setTimeout(function (){vm.CheckBoxBootstrap()}, 100);
                    setTimeout(function (){vm.RadioButtonBootstrap()}, 500);
                })
        },
        DeleteActivityData: function (id) {
            var vm = this;
            if (id > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, null, function () {

                    var postJSON = {
                        ids: id,
                        moduleName: vm.$route.params.moduleName,
                        subModuleCode: vm.$route.params.subModuleCode,
                        refCode: 'DELETE_VIEW_ACTIVITY',
                        refId: vm.$route.params.id,
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    LeadDataService.CommonDelete(objectPostString).then(function (response) {
                        if (response.data[0].Status == "Success") {
                            vm.ShowAlert(vm.$t("DeletedSuccess", [vm.$t("Activity")]),'success',true,vm.$t('Alert'));
                            vm.loadData("activities");
                        }
                        else {
                            vm.ShowAlert(vm.$t("DeletedError",  [vm.$t("Activity")]),'failure',true,vm.$t('Alert'));
                        }
                    },);

                });
            }
        },
        openActivityModal(item) {
            if (item.activity_type == "Task") {
                this.showAddTaskModal = true;
                this.modalTaskItem = item;
            }
            else if (item.activity_type == "FollowUp") {
                this.showAddEventModal = true;
                this.modalFollowUpItem = item;
            }
            else if (item.activity_type == "Call") {
                this.showCallModal = true;
                this.modalCallItem = item;
            }
        },
        openCRMContactModal: function (contactDetails) {
            var type = 'AddContact';
            if (contactDetails != null) {
                type = 'UpdateContact';
            }
            if ((typeof this.checkForAdditionalPermission !== 'undefined') && !this.checkForAdditionalPermission(type)) {
                alertR(Globalize.localize("additionalOwnerAuthorityWarning"), true, Globalize.localize('Alert'));
            } else {
                this.iscontactDetails = contactDetails;
                this.showContactModal = (this.showContactModal) ? false : true;
            }
        },
        DeleteActivity(item) {
            this.modalDeleteItem = item;
            this.DeleteActivityData(this.modalDeleteItem.followup_notification_id);
        },
        FetchAddressdata: function (item) {
            var vm = this;
            vm.addressdetail = [];
            vm.addressdetail.push(item);
            setTimeout(function () {
                vm.showAddAddress = true;
            }, 500);
           
        },
        openCRMExistingContactModel: function () {

            if ((typeof this.checkForAdditionalPermission !== 'undefined') && !this.checkForAdditionalPermission("AddContact")) {
                alertR(Globalize.localize("additionalOwnerAuthorityWarning"), true, Globalize.localize('Alert'));
            } else {
                this.showExistingContactModal = (this.showExistingContactModal) ? false : true;
            }
        },
        DeleteAddressdata: function (item) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, null, function (result) {
                item.actiontype = 'DELETE';
                var objectPostString = JSON.stringify(item);
                LeadDataService.SaveAccountAddress(objectPostString).then(function (response) {
                    if (response.data.Status == "DELETE") {
                        vm.ShowAlert(vm.$t("Address has been deleted Successfully."),"success", true, vm.$t("Alert"));
                        vm.loadData('address');
                    }
                    else {
                        vm.ShowAlert(vm.$t('ErrorTryAgain'),"failure", true, vm.$t("Alert"));
                    }
                     
                });
            })
        },
        loadData: function (tabName, searchCondition) {
            $("#AddProducts").hide();
            this.DisableButton();
            $("#AddProducts").hide();
            var vm = this;
            if (tabName != "" && tabName != null) {
                 
                var url = '';
                if (tabName == "attachments") {

                    vm.HeaderTitle = vm.$t('Attachments');
                    vm.isAttachment = true;
                    vm.IsEdit = false;
                    vm.isProduct = false;
                    vm.IsAddress = false;
                    LeadDataService.AttachmentListing(vm.ApiURL).then(function (response) {
                         
                        if (response.data != null) {
                            vm.attachmentData = response.data.data;
                            setTimeout(function (){vm.CheckBoxBootstrap()}, 100);
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                    vm.activeKey = 5;
                }

                else if (tabName == "contacts" || tabName == "LoginContact") {
                    if (tabName == "contacts") {
                        vm.HeaderTitle = "Contacts";
                        vm.location = "contacts";
                        vm.IsContact = true;
                    }
                    else {
                        vm.HeaderTitle = "Login Access";
                        vm.location = "LoginContact";
                        vm.IsContact = false;
                    }
                    vm.IsEdit = false;
                    vm.isAttachment = false;
                    vm.isProduct = false;
                    vm.IsAddress = false;

                    var url = `id=${vm.$route.params.id}&subModuleCode=${vm.$route.params.subModuleCode}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
                    if (!vm.isBlank(searchCondition)) {
                        url = url + "&searchCondition=" + searchCondition
                    }
                    LeadDataService.ContactListing(url).then(function (response) {
                         
                        if (response.data != null) {
                            vm.contactsData = response.data.data;
                            vm.contactsHeader = response.data.schema;
                            setTimeout(function (){vm.CheckBoxBootstrap()}, 100);
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                            vm.contactsData = null;
                        }
                    });
                    vm.activeKey = 2;
                }                
                else if (tabName == "products") {
                    var vm = this;
                    vm.HeaderTitle = vm.$t('Products');
                    vm.isProduct = true;
                    vm.IsEdit = false;
                    vm.IsAddress = false;
                    vm.isAttachment = false;
                    $("#AddProducts").show();
                    url = `id=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
                    if (!vm.isBlank(searchCondition)) {
                        url = url + "&searchCondition=" + searchCondition;
                    }
                    LeadDataService.getItemListForAssociate(url).then(function (response) {
                         
                        if (response.data != null && response.data != "") {
                            vm.productData = response.data.data;
                            setTimeout(function (){vm.CheckBoxBootstrap()}, 100);
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                            vm.productData = [];
                        }
                    });
                    vm.activeKey = 3;
                }
                else if (tabName == "deals") {
                    vm.HeaderTitle = vm.$t('Deals');
                    vm.IsEdit = false;
                    vm.isProduct = false;
                    vm.IsAddress = false;
                    vm.isAttachment = false;
                    url = `id=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
                    LeadDataService.GetDealListing(url).then(function (response) {
                         
                        if (response.data != null) {
                            vm.dealsHeader = response.data.schema;
                            if (response.data.data.length > 0) {
                                vm.dealsData = response.data.data;
                                setTimeout(function (){vm.CheckBoxBootstrap()}, 100);
                                vm.noRecordForTab = false;
                            }
                            else {
                                vm.noRecordForTab = true;
                                vm.dealsData = null;
                            }
                        } else {
                            vm.noRecordForTab = true;
                            vm.dealsData = null;
                        }
                    });
                    vm.activeKey = 6;
                }
                else if (tabName == "timeline") {
                      vm.IsEdit = true;
                      vm.isProduct = false;
                      vm.IsAddress = false;
                      vm.isAttachment = false;
                      vm.FetchData();
                      vm.activeKey = 1;   
                    }
                else if (tabName == "address") {
                    vm.HeaderTitle = "Additional Address";
                    vm.IsEdit = false;
                    vm.isProduct = false;
                    vm.IsAddress = true;
                    vm.isAttachment = false;
                    url = `sourceId=${vm.$route.params.id}&subModuleCode=${vm.$route.params.subModuleCode}&moduleName=${vm.$route.params.moduleName}`;
                    LeadDataService.AccountAddressListing(url).then(function (response) {
                        if (response.data != null && response.data.data.length != 0) {
                            vm.addressData = response.data.data;
                            setTimeout(function (){vm.CheckBoxBootstrap()}, 100);
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                    vm.activeKey = 4;
                }
            }
        },
        onCloseAddress(){
            this.showAddAddress = false;
            this.addressdetail=[];
        },
        onCloseContacts(){
            this.showContactModal=false;
            this.loadData('contacts');
        }
    }
}
</script>

<template>
    
    <div id="divMainFormData" v-cloak>
        <section class="main-content" v-cloak>
            <div class="container-fluid padding-t_10">
                <div class="col-md-12 p-0 mt-0 bg_outerdark">
                </div>
            </div>
           
        </section>
        <section id="main-content" class="main-content">
            <input id="hdnTId" value="455306" type="hidden">
            <input id="hdnLoggedInUserType" value="CA" type="hidden">
            <input id="hdnstringticketid" value="jF/iV88Awo2qrk9eQNz0wA==" type="hidden">
            <input id="hdnTicketDepartmentId" value="341707" type="hidden">
            <input id="hdnUserDepartmentId" value="341709" type="hidden">
            <input id="hdnTicketStatus" value="TICKET_CLOSEDLOCKED" type="hidden">
            <input id="hdnTicketOpenDate" value="10/25/2018 6:19:25 AM" type="hidden">
            <section class="main-content">
                <loader :is-visible="isLoading"></loader>
                    <div class="left-menu-tab">
                        <div class="col-md-12 p-0 ">
                            <div class="theme-primary partition">
                                <span class="p-name text-white textelipsis">{{accountName}}
                                </span>
                            </div>
                        </div>
                        <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab" id="myTab"
                            role="tablist">
                            <li class="nav-item">
                                <a class="nav-link left-menu" :class="{ 'active': activeKey == 1 }"
                                    data-bs-target="#tab-details-content" href="javascript:void(0)" data-bs-toggle="tab"
                                    role="tab" id="tab-Details" v-on:click="loadData('timeline'); DisableButton()">
                                    <em class="fa fa-list" aria-hidden="true" aria-selected="true"></em>
                                    {{ $t('Detail') }}
                                </a>
                            </li> 
                            <li class="nav-item">
                                <a class="nav-link left-menu" :class="{ 'active': activeKey == 2 }"
                                    href="javascript:void(0)" data-bs-target="#tab-Contact-content" id="tab-Contact"
                                    data-toggle="tab" role="tab" v-on:click="loadData('contacts')"
                                    aria-selected="false"><em class="fa fa-phone" aria-hidden="true"></em>{{
                                    $t('Contacts') }}</a>
                            </li>
                            <li class="nav-item" v-if="$route.params.AccountCode=='NEW_PROVIDER'">
                                <a class=" nav-link left-menu" :class="{ 'active': activeKey == 3 }"
                                    href="javascript:void(0)" data-bs-target="#tab-products-content" id="tab-products"
                                    data-toggle="tab" role="tab" v-on:click="loadData('products')"><em    class="fa fa-shopping-cart" aria-hidden="true" aria-selected="false"></em>{{
                                    $t('Products') }}</a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link left-menu" :class="{ 'active': activeKey == 4 }" id="tab-Address"
                                    data-toggle="tab" role="tab" href="javascript:void(0)"
                                    data-bs-target="#tab-address-content" v-on:click="loadData('address')"><em    class="fa fa-vcard" aria-hidden="true" aria-selected="false"></em>{{
                                    $t('AdditionalAddresses') }}</a>
                            </li>

                            <li class="nav-item">
                                <a class="nav-link left-menu" :class="{ 'active': activeKey == 5 }"
                                    href="javascript:void(0)" data-bs-target="#tab-attachment-content"
                                    id="tab-attachment" data-toggle="tab" role="tab"
                                    v-on:click="loadData('attachments')"><em class="fa fa-file" aria-hidden="true"
                                        aria-selected="false"></em>{{ $t('Attachments') }} </a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link left-menu" :class="{ 'active': activeKey == 6 }"
                                    href="javascript:void(0)" data-bs-target="#tab-deal-content" id="tab-deal"
                                    data-toggle="tab" role="tab" v-on:click="loadData('deals')"> <em    class="fa fa-handshake-o" aria-hidden="true" aria-selected="false"></em>{{
                                    $t('Deal') }}</a>
                            </li>
                           

                        </ul>
                    </div>
                    <div class="right-content">
                        <div class="col-md-12 p-0">
                            <div class="theme-primary partition p-actions-expand">
                                <span class="p-name text-white">{{accountName}}-{{HeaderTitle}}</span>
                                <span class="p-actions float-right">

                                    <a href="javascript:void(0)" v-bind:class="{'p-action-btn text-white btn_add':true}"
                                        v-if="IsAddress" v-on:click="OpenAddress()"><em class="fa fa-plus pr-2"></em><br /> {{ $t('AddAddress') }}</a>                                   
                                    <a href="javascript:;" id="AssociateProduct" v-if="isProduct"
                                        class="p-action-btn text-white formbtn mr-1" :title="$t('AssociateProducts')"
                                        v-on:click="openCRMProductModel"><em class="fa fa-plus"></em><br />{{
                                        $t('AssociateProducts') }}</a>


                                    <a href="javascript:;" v-bind:class="{'p-action-btn text-white':true}" title=""
                                        id="ancmergeloaddata" v-on:click="openCRMExistingContactModel()"
                                        v-if="IsContact" data-original-title="@Resources.AssociateContact"><em        class="fa fa-plus"></em><br />{{ $t('AssociateContact') }}</a>

                                    <a href="javascript:;" v-bind:class="{'p-action-btn text-white':true}"
                                        title="@Resources.UploadFile" v-if="isAttachment"
                                        v-on:click="openCRMAttachmentModal(null);"><em class="fa fa-plus"></em><br />{{
                                        $t('Add') }} {{ $t('Attachment') }}</a>
                                    <RouterLink :to="'/CRM/Accounts'"  title="" id="backtolist"
                                        class="p-action-btn text-white float-right"
                                        :data-original-title="$t('BacktoList')"><em        class="fa fa-angle-double-left pr-2"></em><br />{{ $t('BacktoList') }}</RouterLink>
                                        <RouterLink :to="editUrl"
                                            v-if="IsEdit"
                                             class="p-action-btn text-white">
                                            <em class="fa fa-pencil"></em><br />{{$t('Edit')}}
                                        </RouterLink>
                                </span>
                                <span class="collapse-head-btns">
                                    <a href="javascript:;" class="collapse-btns"><em class="fa fa-angle-up text-white"></em></a>
                                </span>
                            </div>
                        </div>
                        <div class="tab-pane mb-0">
                            <div class="tab-content fsmtabcontrolMobileView" id="myTabContent">
                                <div class="expand-list">
                                    <a href="javascript:;"
                                        class="expand-list-btn justify-content-center align-items-center">
                                        <em class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></em>
                                    </a>
                                </div>

                                <div class="tab-pane fade p-0 border-0" :class="{ 'active show': activeKey == 5}"
                                    id="tab-attachment-content" role="tabpanel"
                                    aria-labelledby="tab-attachment-content">
                                    <crmattachment v-bind:attachmentdata="attachmentData" :sourceid="$route.params.id">
                                    </crmattachment>
                                </div>
                                <div class="tab-pane fade p-0 border-0" role="tabpanel"
                                    aria-labelledby="tab-products-content" id="tab-products-content"
                                    :class="{ 'active show': activeKey == 3}">
                                    <accountproduct v-bind:productdata="productData"
                                        :sourceid="$route.params.id" :submodulecode="$route.params.subModuleCode"
                                        :modulename="$route.params.moduleName"></accountproduct>
                                </div>


                                <div class="tab-pane fade p-0 border-0" id="tab-Contact-content" role="tabpanel"
                                    aria-labelledby="tab-Contact-content" :class="{ 'active show': activeKey == 2}">

                                    <accountcontacttab v-bind:contactsdata="contactsData"
                                        v-bind:contactsheader="contactsHeader"  :sourceid="$route.params.id" :submoduleCode="$route.params.subModuleCode" 
                                        :modulename="$route.params.moduleName" :companyid="companyId">
                                    </accountcontacttab>
                                </div>


                                <div class="tab-pane fade p-0 border-0" id="tab-deal-content" role="tabpanel"
                                    aria-labelledby="tab-deal-content" :class="{ 'active show': activeKey == 6}">
                                    <div class="listing mt-2">
                                        <div class="table-responsive" v-if="!noRecordForTab">
                                            <table class="table table-bordered dt-responsive nowrap mt-0"
                                                id="table-salesOrder-content" aria-describedby="accountdetail">
                                                <thead class="thead-bg">
                                                    <tr>
                                                         <th scope="col" v-for="(item,itemIndex1) in dealsHeader" :key="itemIndex1">
                                                            <span v-if="item.VISIBLE"
                                                                v-bind:id="item.COLUMN_NAME">{{$t(item.COLUMN_NAME,item.DISPLAY_NAME)}}</span>
                                                        </th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr v-for="(item,index) in dealsData" :key="index">
                                                    <td v-for="(key,index2) in dealsHeader"  v-bind:class="{'text-center':key.COLUMN_NAME=='status_name'}" :key="index2">
                                                        <span v-if="(key.DATA_TYPE=='date' || key.DATA_TYPE=='datetime') && (key.DATA_TYPE !='decimal')" v-bind:class="{'px-2 bg-info text-white d-block': (key.COLUMN_NAME=='status_name' && item[key.COLUMN_NAME] == 'Active'),'px-2 bg-danger text-center text-white d-block': (key.COLUMN_NAME=='status_name' && item[key.COLUMN_NAME] == 'Closed')}">{{ item[key.COLUMN_NAME] | formatDate}}</span>
                                                        <span v-if="(key.DATA_TYPE!='date' && key.DATA_TYPE!='datetime' && key.COLUMN_NAME !='deal_name' )" v-bind:class="{'px-2 bg-info text-white d-block text-center': (key.COLUMN_NAME=='status_name' && item[key.COLUMN_NAME] == 'Active'),'px-2 bg-danger text-white d-block text-center': (key.COLUMN_NAME=='status_name' && item[key.COLUMN_NAME] == 'Inactive')}">{{item[key.COLUMN_NAME]}}</span>
                                                        <span v-if="(key.DATA_TYPE!='date' && key.DATA_TYPE!='datetime' && key.COLUMN_NAME =='deal_name' )" v-bind:class="{'px-2 bg-info text-white d-block text-center': (key.COLUMN_NAME=='status_name' && item[key.COLUMN_NAME] == 'Active'),'px-2 bg-danger text-white d-block text-center': (key.COLUMN_NAME=='status_name' && item[key.COLUMN_NAME] == 'Inactive')}">
                                                            <a href="javascript:;" v-on:click="openDealView(item);">{{item[key.COLUMN_NAME]}}</a></span>
                                                    </td>
                                                </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                    <div v-if="noRecordForTab" class="text-center text-danger p-4 border no-record">{{$t('NoRecordfound')}} </div>
                                </div>
                                <div class="tab-pane p-0 pb-2 border-0" :class="{ 'active show': activeKey == 1 }"
                                    id="tab-details-content" role="tabpanel" aria-labelledby="tab-details-content">
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left panel1 my-4">
                                        <div class="panel1"  v-for="(group,groupindex) in LeftViewData" :key="groupindex">
                                            <div class="heading-border">
                                                <h5 class="h5 mb-2"><span>
                                                    {{GetFormSchemaFieldName(group.group_name,group.group_display_name)}}</span></h5>
                                            </div>
                                         <div class="row">
                                                <div v-bind:class="{'col-md-12 col-lg-12':item.name == 'Company_Description','col-md-6 col-lg-3':item.name != 'Company_Description'}"
                                                v-for="(item, itemindex) in group.LeftViewData" :key="itemindex">
                                                    <div class="form-group">
                                                        <label>
                                                            <strong>{{GetFormSchemaFieldName(item.name, item.display_name)}}:</strong> 
                                                            <span class="answer rightlabeltext ml-1"
                                                                v-if="item.dt_code == 'date' || item.dt_code == 'datetime'">{{$options.filters.formatDate(item.value)}}</span>
                                                            <span class="answer rightlabeltext ml-1"
                                                                v-if="item.dt_code == 'decimal'">{{isBlank(item.value)?"0.00":$options.filters.currency(item.value)}}</span>
                                                            <span class="answer rightlabeltext ml-1"
                                                                v-if="item.dt_code != 'date' && item.dt_code != 'datetime'  && item.dt_code != 'decimal' && item.picklist_options != 'Lookup'">{{
                                                                isBlank(item.value)?"--":(item.value)}}</span>
                                                            <span class="answer rightlabeltext ml-1"
                                                                v-if="item.dt_code != 'date' && item.dt_code != 'datetime'  && item.dt_code != 'decimal' && item.picklist_options == 'Lookup'">{{
                                                                isBlank(item.value)?"--":(item.value)}}</span>
                                                        </label>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                                <div class="tab-pane fade p-0 border-0" id="tab-address-content" role="tabpanel"
                                    aria-labelledby="tab-openactivities-content"
                                    :class="{ 'active show': activeKey == 4 }">
                                    <div class="listing mt-2">
                                        <div class="table-responsive" v-if="!noRecordForTab">
                                            <table class="table table-bordered dt-responsive nowrap mt-0"
                                                id="table-address-content" aria-describedby="addresscontent">
                                                <thead class="thead-bg">
                                                    <tr>
                                                         <th scope="col" class="text-center"><span>Serial No.</span></th>
                                                         <th scope="col"><span>{{$t("AddressName")}}</span></th>
                                                         <th scope="col"><span>{{$t("Address")}}</span></th>
                                                         <th scope="col"><span>{{$t("Description")}}</span></th>
                                                         <th scope="col" class="text-center"><span>{{$t("Edit")}}</span></th>
                                                         <th scope="col" class="text-center"><span>{{$t("Delete")}}</span></th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr v-for="(item,itemIndex1) in addressData" :key="itemIndex1">
                                                        <td class="text-center">{{index+1}}</td>
                                                        <td>{{item.ADDRESS_NAME}}</td>
                                                        <td>{{item.USER_ADDRESS_1}} {{item.USER_ADDRESS_2}}</td>
                                                        <td>{{item.USER_ADDRESS_DESC}}</td>
                                                        <td class="text-center single-action"><a href="javascript:;"
                                                                v-on:click="FetchAddressdata(item)"
                                                                :title="$t('Edit')"><em                                class="fa fa-pencil text-success action-icon pr-2 ml-3"></em></a>
                                                        </td>
                                                        <td class="text-center single-action "><a href="javascript:;"
                                                                :title="$t('Delete')"
                                                                v-on:click="DeleteAddressdata(item)"><em                                class="fa fa-trash action-icon text-danger"></em></a>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                        <div v-if="noRecordForTab" class="text-center text-danger p-4 border no-record">
                                            {{$t("NoRecordfound")}}</div>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
              
            </section>
        </section>
        <modalattachment v-if="showAttachmentModal" @close="showAttachmentModal = false" :userid="userid"
            :companyid="companyId" :id="$route.params.id" :modulename="$route.params.moduleName"
            :submodulecode="$route.params.subModuleCode" :containername="containername" imagesizedata="">
            <h5 slot="header" class="modal-title">{{$t('Attachments')}}</h5>
        </modalattachment>
        <modalproduct v-if="showProductModal" :sourceid="$route.params.id" :submodulecode="$route.params.subModuleCode"
            :modulename="$route.params.moduleName" userinfo="userid" companyid="companyId"
            @close="showProductModal = false">
            <h5 slot="header" class="modal-title">{{$t("Products")}}</h5>
        </modalproduct>

        <AccountModalContact v-if="showContactModal" @close="onCloseContacts" :sourceid="$route.params.id"
            :submodulecode="$route.params.subModuleCode" :modulename="$route.params.moduleName" :userinfo="userid"
            :iscontactDetails="iscontactDetails" :companyid="companyId" sourcename="Account" :DateFormat="DateFormat"></AccountModalContact>
        <modalexistingcontact v-if="showExistingContactModal" :sourceid="$route.params.id"
            :submodulecode="$route.params.subModuleCode" :modulename="$route.params.moduleName" :userinfo="userid"
            :companyid="companyId" @close="showExistingContactModal = false" sourcename="Account">
            <h5 slot="header" class="modal-title">{{ $t('Contacts') }}</h5>
        </modalexistingcontact>


        <modaladdaddress v-if="showAddAddress" @close="onCloseAddress" v-bind:addressdata="addressdetail">
        </modaladdaddress>
    </div>
</template>
