<script>
    import AccountDataService from "../../../services/DataService";
    import modalContactLogin from "../Accounts/modalContactLogin.vue"
export default {
    components:
    {
        modalContactLogin
    },
    props: {
        contactsdata: {
            type: Array,
            required: true
        },
        contactsheader: {
            type: Array,
            required: true
        },
        sourceid: {
            type: String,
            required: true
        },
        submoduleCode: {
            type: String,
            required: true
        },
        companyid: {
            type: Number,
            required: true
        },
        modulename: {
            type: String,
            required: true
        }        
    },
    data: function () {
        
        return {
            ContactsData: [],
            noRecordForTab: true,
            LeadStatus: '',
            ContactsHeader: [],
            IsPrimary: false,
            IsLogin: false,
            contactsearch: '',
            SelectedRow: 0,
            SearchLenght: 0,
            location: "",
            parentid: '',
            IsContactLogin: false,
            contactsLoginData: [],
            contactsLoginHeader: [],
            minwidth: {},
            AccType: '',
            isLoading:false
        }
    },
    watch: {
        contactsheader: function (value) {
            var objdata = [];
            $.each(value, function (index, item) {
                if (item.COLUMN_NAME !== 'first_name')
                    if (item.COLUMN_NAME !== 'last_name')
                        objdata.push(item);
            });
            this.ContactsHeader = JSON.parse(JSON.stringify(objdata));
        },
        contactsdata: function (value) {
            var vm = this;
            
            const urlParams = new URLSearchParams(window.location.search);
            vm.parentid = urlParams.get('id');
            vm.AccType = urlParams.get('accountcode');
            if (vm.location != vm.$parent.location)
                vm.contactsearch = "";
            vm.location = vm.$parent.location;
            vm.ContactsData = value;
            $.each(value, function (index, item) {
                item.isDisable = false;
                if (item.IsPrimary == 1) {
                    item.isDisable = true;
                    vm.SelectedRow = item.contact_id;
                }             
            });
            vm.ContactsData = JSON.parse(JSON.stringify(value));
            try {
                vm.LeadStatus = vm.$parent.LeadStatus;
            } catch (ex) { }
            vm.noRecordForTab = ((vm.ContactsData != null && vm.ContactsData.length > 0) ? false : true);
            setTimeout(function(){vm.ResponsiveDataTable('table-contacts-content')}, 500);
        }
    },
    updated: function () {
        this.BindActionButtonEvent();
    },
    methods: {

        
        AssociateLicense: function (item) {
            $("#huserid").val(item.USER_ID);
            $("#divLicenseModal").dialog("open");
        },
        OpenContactLogin: function (item) {
            debugger;
            var vm = this;           
            if ($.trim(item.email_id) == "") {
                vm.ShowAlert(vm.$t("Please fill email first."), "warning", true,vm.$t('Alert'));
                return false;
            }
            vm.IsContactLogin = true;
            setTimeout(function () {
                vm.contactsLoginData = [];
                vm.contactsLoginHeader = [];
                var obj = [];
                obj.push(item)
                vm.contactsLoginData = obj;
                var obj1 = [];
                $.each(vm.contactsheader, function (ind, itm) {
                    if (itm.COLUMN_NAME == 'email_id')
                        obj1.push(itm);
                });
                vm.contactsLoginHeader = obj1;
               
            }, 500);
        },
        SendClientMail:function(item){
            var vm = this;
            if(item.IsLogin || item.USER_ID!=null)
            {
                vm.isLoading=true;
                AccountDataService.SendActivationMail('userId='+item.USER_ID).then(function(response){
                    vm.isLoading=false;
                    if(response.data.status==0){
                        vm.ShowAlert(vm.$t('SuccessActivationMailToCL'),"success",true,vm.$t('Alert'));
                       
                    }
                    else
                    {
                    vm.ShowAlert(vm.$t('FailureActivationMailToCL'),"failure",true,vm.$t('Alert'));                    
                    }
                    vm.$parent.loadData(vm.location);
                })
            }
        },
        deleteClient: function (item) {
            var vm = this;
            var data = {
                cid: item.contact_id,
                uid: (item.USER_ID == null ? 0 : item.USER_ID)
            };
            vm.confirmR(vm.$t("ConfirmDelete"), true, null, function (result) {
                    var jsonData = JSON.stringify(data);
                    AccountDataService.DeleteClient(jsonData).then(function (response) {
                        if (response.data.Status == "SUCCESS") {
                            vm.ShowAlert(vm.$t("Contact has been successfully deleted."), "success", true, vm.$t("Alert"));
                            vm.contactsearch = "";
                            vm.$parent.loadData(vm.location);
                        }
                    });
             
            });
        },
        SaveAccount: function (item,hittype) {
            if (item.IsLogin || item.USER_ID != null || hittype == "direct" ) {
                var vm = this;
                if (item.RoleID == "0")
                    item.RoleID = null;
                var objectstring = JSON.stringify(item);
                vm.IsLogin = item.IsLogin;
                AccountDataService.SaveClientInfo(objectstring ).then(function (response) {
                    if (response.data != "") {
                        if (response.data.msg == "SUCCESS") {
                            item.USER_ID = response.data.MAIN_ID
                            if (hittype!="direct")
                            vm.ShowAlert(vm.$t("User has been created successfully"),"success", true, vm.$t("Alert"));
                            if (vm.IsLogin) {
                            }
                        }
                        else if (response.data.msg == "UPDATE") { 
                            if (hittype != "direct")
                            vm.ShowAlert(vm.$t("User has been updated successfully"),"success", true, vm.$t("Alert"));
                            vm.$parent.loadData(vm.location);
                        }
                        else if (response.data.msg == "EmailExist") {
                            if (hittype != "direct")
                            vm.ShowAlert(vm.$t("Email ID already exist. Please change the email ID"),"warning", true, vm.$t("Alert"));
                            vm.$parent.loadData(vm.location);

                        }
                        else {
                            if (hittype != "direct")
                            vm.ShowAlert(vm.$t('DeletedError',[vm.$t('User')]), "failure", true, vm.$t("Alert"));
                        }

                    }
                     
                });

            }
            else {
                vm.ShowAlert(vm.$t("Please enable login."), "warning", true, vm.$t("Alert"));
            }
        },
        Searchdata: function () {
            var data = "";
            if ($.trim(this.contactsearch) != "") {
                this.SearchLenght = $.trim(this.contactsearch).length;
                data = " CONCAT(cont.first_name, ' ', cont.last_name,' ',cont.email_id) LIKE '%" + this.contactsearch + "%'";
            }
            this.$parent.loadData(this.location, encodeURIComponent(data).replace(/'/g, "%27"));
        },
        cancelOrRefresh: function () {
            this.contactsearch = "";
            this.$parent.loadData(this.location);
        },
        SearchTextData: function (event) {
            if (event.keyCode == 13) {
                this.Searchdata();
            }
        },
        
        SetLoginOrPrimary: function (itm, event) {
            debugger;
            var vm = this;
            vm.confirmR(vm.$t("MessageLogin"), true, null, function (result) {
                if (itm.IsPrimary) {
                    $.each(vm.ContactsData, function (index, item) {
                        if (item.contact_id != itm.contact_id) {
                            item.IsPrimary = false;
                            item.isDisable = false;
                        }
                        else {
                            if (item.USER_ID != null && item.USER_ID != "") {
                                item.IsLogin = true;
                                item.isDisable = true;
                                vm.ShowAlert("This is a primary contact. You can't disable login.", "warning", true, vm.$t("Alert"));
                            }
                        }
                    });
                }
                if (!itm.IsPrimary) {
                    itm.isDisable = false;
                }
            });

        },
        ChangeLeadOwner: function (id) {            
            var varObj = this;
            varObj.$parent.OwnerIds.push(id);
            this.$parent.showLeadOwnerModal = (this.$parent.showLeadOwnerModal) ? false : true;
        },        
        openContactModal: function (contactDetails) {
            var vm=this;
            var type = 'AddContact';
            if (contactDetails != null) {
                type = 'UpdateContact';
            }
            if ((typeof this.$parent.checkForAdditionalPermission !== 'undefined') && !this.$parent.checkForAdditionalPermission(type)) {
                alertR(vm.$t("additionalOwnerAuthorityWarning"),'warning', true, vm.$t('Alert'));
            } else {
                this.$parent.iscontactDetails = contactDetails;
                this.$parent.showContactModal = (this.$parent.showContactModal) ? false : true;
            }
        },
        deleteContactFromTab: function (value) {
            var vm=this;
            if ((typeof this.$parent.checkForAdditionalPermission !== 'undefined') && !this.$parent.checkForAdditionalPermission('DeleteContact')) {
                alertR(vm.$t("additionalOwnerAuthorityWarning"),'warning', true, vm.$t('Alert'));
            } else {
                var ths = this;
                var vm = checkVue;
                if (value > 0) {
                    vm.confirmR(vm.$t("ConfirmDeAssociateCommon")+ vm.$t('Contact'), function (result) {
                        if (result) {
                            var postJSON = {
                                ids: value,
                                moduleName: ths.modulename,
                                subModuleCode: ths.submodulecode,
                                refCode: 'DELETE_VIEW_CONTACTS',
                                refId: ths.sourceid
                            };

                            var objectPostString = JSON.stringify(postJSON);
                            AccountDataService.CommonDelete(objectPostString).then(function (response) {
                                var box = "";
                                if (response.data[0].Status == "Success") {
                                    $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                    vm.$parent.loadData('contacts');
                                    box = vm.ShowAlert(vm.$t('TeamDeassociateSuccess',[vm.$t('Contact')]), "success", true, vm.$t("Alert"));
                                }
                                else {
                                    box = vm.ShowAlert(vm.$t('ErrorDeassociateFailure',[vm.$t('Contact')]), "failure", true, vm.$t("Alert"));
                                }
                                setTimeout(function () {
                                    box.modal('hide');
                                }, 2000);
                                 
                            });
                        }
                    });
                }
                else {
                    vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Contact')]), "failure", true, vm.$t("Alert"));
                }
            }
        },
        bindStyle: function (item) {
            var styleObj = {};
            if (item == "personal_notes") {
                styleObj = {
                    'min-width': '200px',
                }
            }
            else {
                styleObj = {
                    'min-width': '120px',
                }
            }
            return styleObj;
        },
    }
}
</script>
<template>
    <div>    
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 searchpanel tablehead search float-left p-0">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 Search-panel float-left py-2 p-0">
                <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
                    <div class="row mt-0">
                        <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pr-1 pl-0">
                            <div class="form-group">
                                <input type="search" v-model="contactsearch" v-on:keyup="SearchTextData($event)"
                                    class="form-control" :placeholder="$t('searchByNameOrEmail')" />
                            </div>
                        </div>
                        <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pl-0">
                            <div class="search-btm-btn background-0 text-left form-check-inline">
                                <a title="" v-on:click="Searchdata()" href="javascript:;" class="btn btn-success mr-1 px-4"
                                    data-toggle-tooltip="tooltip" data-original-title="Search"><em    class="fa fa-search"></em></a>
                                <a href="javascript:;" title="" v-on:click="cancelOrRefresh()"
                                    class="btn btn-danger clearsearchtext px-4" data-toggle-tooltip="tooltip"
                                    data-original-title="Clear"><em class="fa fa-refresh"></em></a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="listing">
            <div class="table-responsive table-fix-header">
                <table class="table-sorting table table-bordered mb-0" id="table-contacts-content" aria-describedby="accountcontact">
                    <thead class="thead-bg">
                        <tr>
                             <th scope="col" style="width:50px !important;" v-if="submoduleCode=='CRM_CAMPAIGNS'" class="text-center">
                                <input id="chkAll" class="chkitems" value="" type="checkbox"></th>
                             <th scope="col" class="text-capitalize text-left" style="padding: 10px 8px !important;"><span>Name</span>
                            </th>
                             <th scope="col" class="text-capitalize text-left" style="padding: 10px 8px !important;"><span>Role
                                    Title</span></th>
                             <th scope="col" class="text-capitalize text-left" v-bind:style="bindStyle(item.COLUMN_NAME)"
                                v-for="item in ContactsHeader">
                                <span v-if="item.VISIBLE" v-bind:id="item.COLUMN_NAME">{{$t(item.COLUMN_NAME,
                                    item.DISPLAY_NAME)}}</span>
                            </th>
                             <th scope="col" style="min-width:50px;" class="text-center"><span>Is Email Sent</span></th>
                             <th scope="col" style="min-width:50px;" class="text-center" v-if="submoduleCode=='CRM_ACCOUNTS' "><span>Is
                                    Primary</span></th>
                             <th scope="col" style="min-width:50px;" class="text-center" v-if="submoduleCode=='CRM_ACCOUNTS'">
                                <span>Enable Login</span></th>                           
                             <th scope="col" style="min-width:50px;" class="text-center" v-if="submoduleCode!='CRM_ACCOUNTS'"><span>
                                    {{$t("Edit")}}</span></th>
                             <th scope="col" style="min-width:50px;" class="text-center" v-if="submoduleCode=='CRM_CAMPAIGNS'"><span>
                                    {{$t("ConvertLead")}}</span></th>
                             <th scope="col" style="min-width:50px;" class="text-center" v-if="submoduleCode!='CRM_ACCOUNTS'">
                                <span>DeAssociate</span></th>
                             <th scope="col" style="min-width:50px;" class="text-center" v-if="submoduleCode=='CRM_ACCOUNTS'">
                                <span>Action</span></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-if="noRecordForTab">
                            <td colspan="9" class="text-danger text-center no-record">{{$t("NoRecordfound")}}</td>
                        </tr>
                        <tr v-for="(item, index) in ContactsData" :key="index">
                            <td class="text-center" v-if="submoduleCode=='CRM_CAMPAIGNS'"><input class="chkItems"
                                    v-bind:disabled="item.converted_lead_id!=null" v-bind:value="item.contact_id"
                                    type="checkbox"></td>
                            <td v-bind:class="{'text-left':true}">                               
                                <a href="javascript:;" class="actions-onclick"
                                    v-on:click="openContactModal(item['contact_id'])"><span class="text"
                                        style="color:dodgerblue;cursor: pointer;">{{item.first_name}}
                                        {{item.last_name}}</span></a>
                            </td>
                            <td v-bind:class="{'text-left':true}">
                                <span>{{item.RoleTitle}}</span>
                            </td>
                            <td v-for="key in ContactsHeader" 
                                v-bind:class="{'text-left':true, 'text-center':key.COLUMN_NAME=='status_id'}">
                                <span v-if="key.VISIBLE && (key.DATA_TYPE=='date' || key.DATA_TYPE=='datetime')"
                                    v-bind:class="{'px-2 bg-success text-white d-block': (key.COLUMN_NAME=='status_id' && item[key.COLUMN_NAME] == 'Active'),'px-2 bg-danger text-white d-block': (key.COLUMN_NAME=='status_id' && item[key.COLUMN_NAME] == 'Inactive')}">{{item[key.COLUMN_NAME] | formatDate}}</span>
                                <span v-else-if="key.VISIBLE && (key.DATA_TYPE!='date' && key.DATA_TYPE!='datetime')"
                                    v-bind:class="{'px-2 bg-success text-white d-block': (key.COLUMN_NAME=='status_id' && item[key.COLUMN_NAME] == 'Active'),'px-2 bg-danger text-white d-block': (key.COLUMN_NAME=='status_id' && item[key.COLUMN_NAME] == 'Inactive')}">{{(key.COLUMN_NAME=='gender')?((item[key.COLUMN_NAME]=='M')?'Male':(item[key.COLUMN_NAME]=='F')?'Female':''):item[key.COLUMN_NAME]}}</span>
                            </td>
                            <td class="text-center mb-0"><span> {{ item.IsEmailSent }}</span></td>
                            <td v-if="submoduleCode=='CRM_ACCOUNTS' ">
                                <div class="form-group text-center mb-0">
                                    <label class="switch">                                       
                                        <input type="checkbox" v-model="item.IsPrimary" v-bind:name="'IsPrimary'+index"
                                            v-bind:id="'IsPrimary'+index"
                                            v-on:change="SetLoginOrPrimary(item,$event)"
                                            class="form-check-input rdoticketfor custom-control-input dynamic"
                                            disabled="disabled" style="cursor:default;">
                                        <span class="slider round" style="cursor:default;">
                                            <span class="slider-yes">Yes</span>
                                            <span class="slider-no">No</span>
                                        </span>
                                    </label>
                                </div>
                            </td>
                            <td v-if="submoduleCode=='CRM_ACCOUNTS'">
                                <div class="form-group text-center mb-0">
                                    <label class="switch">
                                        <input class="form-check-input custom-control-input dynamic" type="checkbox"
                                            v-model="item.IsLogin" v-bind:name="'IsLogin'+ index"
                                            v-on:change="SetLoginOrPrimary(item,$event)" disabled="disabled">
                                        <span class="slider round" style="cursor:default;">
                                            <span class="slider-yes">Yes</span>
                                            <span class="slider-no">No</span>
                                        </span>
                                    </label>
                                </div>
                            </td>
                            <td class="text-center mobile-action" v-if="submoduleCode=='CRM_ACCOUNTS'">
                                <span class="actions mobileaction">
                                    <em class="fa fa-ellipsis-h action_icon" aria-hidden="true"></em>
                                    <span class="action-list-box">
                                        <span class="list-actions" id="action-list">
                                            <a class="cursorpointer actions-onclick sendactivationmail" :title="$t('SendOrResendActivationMail')" v-if="item.IsLogin"
                                             v-on:click="SendClientMail(item)"><em class="fa fa-envelope"></em><span class="text">{{ $t("SendOrResendActivationMail")}}</span></a>
                                            <a href="javascript:;" class="actions-onclick" title="Login"
                                                v-on:click="OpenContactLogin(item)"><em class="fa fa-key text-primary action-icon"></em><span
                                                    class="text">Login</span></a>
                                            
                                            <a href="javascript:;" class="actions-onclick"
                                                v-if="item.USER_ID != null || location=='contacts'"
                                                v-on:click="location!='LoginContact'?openContactModal(item['contact_id']):''"
                                                 :title="$t('Edit')"> <em class="fa fa-pencil text-success action-icon pr-2"></em><br /> <span class= "text">{{ $t("Edit")}}  </span></a>
                                            <a href="javascript:;" class="actions-onclick" v-on:click="deleteClient(item)" :title="$t('Delete')" 
                                                    v-if="submoduleCode=='CRM_ACCOUNTS'"> <em class=" fa fa-trash text-danger
                                                action-icon"></em><span class="text"> Delete </span></a>
                                            <em class="fa fa-times close close-action" aria-hidden="true"></em>
                                        </span>
                                    </span>
                                </span>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>    
        <modalContactLogin v-if="IsContactLogin" v-bind:contactsdata="contactsLoginData" @close="IsContactLogin = false"
            v-bind:contactsheader="contactsLoginHeader"  v-bind:companyid="companyid">
        </modalContactLogin>
        <loader :is-visible="isLoading"></loader>
    </div>    
</template>
<style scoped>
    .action-list-box {
        opacity: 0;
        height: 0px;
        transition: opacity 1s;
    }
    .is-visible {
        display: inline-block;
        height: auto;
        overflow: hidden !important;
        }
</style>