<script>
import LeadDataService from "../../../services/DataService";
import $ from "jquery";
export default {
    props: {
            addressdata: {
                type: Array,
                required: true
            },
        },
        data() {
            return {
                countryDDLData: [],
                contactDDLData: [],
                StateDDLData: [],
                CityDDLList: [],
                addressheader: '',
                Addrdata: {
                    useraddrid: 0,
                    ddluserid: '',
                    txtaddr1: null,
                    txtaddr2: null,
                    ddlcountryid: '',
                    txtstate: '',
                    txtcity: '',
                    txtzipcode: null,
                    txtdescription: null,
                    txtaddrname: null,
                    IsDisplay: false,
                    isVisibleToClient: false,
                    AccountId: this.$route.params.id
                }
            }
        },
        watch: {
            addressdata: function (value) {
                var vm = this;
                vm.Addrdata.useraddrid = value[0].useraddrid;
                vm.Addrdata.ddluserid = value[0].USER_ID == null ? "" : value[0].USER_ID;
                vm.Addrdata.txtaddr1 = value[0].USER_ADDRESS_1;
                vm.Addrdata.txtaddr2 = value[0].USER_ADDRESS_2;
                vm.Addrdata.ddlcountryid = value[0].COUNTRY_ID;
                vm.Addrdata.txtstate = value[0].USER_STATE;
                vm.Addrdata.txtcity = value[0].USER_CITY;
                vm.Addrdata.txtzipcode = value[0].USER_ZIPCODE;
                vm.Addrdata.txtdescription = value[0].USER_ADDRESS_DESC;
                vm.Addrdata.txtaddrname = value[0].ADDRESS_NAME;
                vm.Addrdata.IsDisplay = value[0].ADD_TO_DIRECTORY;
                vm.Addrdata.isVisibleToClient = value[0].IS_CLIENT_ADDRESS;
                if (vm.Addrdata.useraddrid != 0) {
                    vm.addressheader = vm.$t('Edit') + " " + vm.$t('Address');
                }
                setTimeout(function () {
                    var cntrydata = vm.countryDDLData;
                    $.each(cntrydata, function (index, element) {
                        if (element.name == "United States")
                            vm.Addrdata.ddlcountryid = vm.Addrdata.ddlcountryid == 0 ? element.value : vm.Addrdata.ddlcountryid;
                    });
                    vm.FillDropdown('txtstate', vm.Addrdata.ddlcountryid, 'EDIT');
                    if (vm.Addrdata.txtstate != '')
                        vm.FillDropdown('txtcity', vm.Addrdata.txtstate, 'EDIT');
                }, 500);
            }
        },
        created: function () {
            var vm = this;
            vm.addressheader = vm.$t('Add') + " " + vm.$t('Address');
            vm.GetCountryValues();
            vm.GetddlContacts();
        
        },
        mounted:function(){
            if (this.addressdata != '')
            {
                this.fetchAddressDetails();
            }
        },
        methods: {
            fetchAddressDetails:function(){
                var vm = this;
                vm.Addrdata.useraddrid = vm.addressdata[0].useraddrid;
                vm.Addrdata.ddluserid = vm.addressdata[0].USER_ID == null ? "" : vm.addressdata[0].USER_ID;
                vm.Addrdata.txtaddr1 = vm.addressdata[0].USER_ADDRESS_1;
                vm.Addrdata.txtaddr2 = vm.addressdata[0].USER_ADDRESS_2;
                vm.Addrdata.ddlcountryid = vm.addressdata[0].COUNTRY_ID;
                vm.Addrdata.txtstate = vm.addressdata[0].USER_STATE;
                vm.Addrdata.txtcity = vm.addressdata[0].USER_CITY;
                vm.Addrdata.txtzipcode = vm.addressdata[0].USER_ZIPCODE;
                vm.Addrdata.txtdescription = vm.addressdata[0].USER_ADDRESS_DESC;
                vm.Addrdata.txtaddrname = vm.addressdata[0].ADDRESS_NAME;
                vm.Addrdata.IsDisplay = vm.addressdata[0].ADD_TO_DIRECTORY;
                vm.Addrdata.isVisibleToClient = vm.addressdata[0].IS_CLIENT_ADDRESS;
                if (vm.Addrdata.useraddrid != 0) {
                    vm.addressheader = vm.$t('Edit') + " " + vm.$t('Address');
                }
                setTimeout(function () {
                    var cntrydata = vm.countryDDLData;
                    $.each(cntrydata, function (index, element) {
                        if (element.name == "United States")
                            vm.Addrdata.ddlcountryid = vm.Addrdata.ddlcountryid == 0 ? String(element.value) : String(vm.Addrdata.ddlcountryid);
                    });
                    vm.FillDropdown('txtstate', vm.Addrdata.ddlcountryid, 'EDIT');
                    if (vm.Addrdata.txtstate != '')
                        vm.FillDropdown('txtcity', vm.Addrdata.txtstate, 'EDIT');
                }, 500);
                var frmFields={}
                     frmFields['Address1']=vm.Addrdata.txtaddr1;
                     frmFields['Address2']=vm.Addrdata.txtaddr2;
                     frmFields['Country']=vm.Addrdata.ddlcountryid;
                     frmFields['State']=vm.Addrdata.txtstate;
                     frmFields['City']=vm.Addrdata.txtcity;
                     frmFields['ZipCode']=vm.Addrdata.txtzipcode;
                     frmFields['txtdescription']=vm.Addrdata.txtdescription;
                     frmFields['AddressName']=vm.Addrdata.txtaddrname; 
                     frmFields['ContactName']=vm.Addrdata.ddluserid;                    
                    //  vm.$refs.form.resetForm({values:frmFields});
            },
            OnChangeDropdown: function (event, type) {
                this.FillDropdown(type, event.target.value);
            },
            FillDropdown: function (type, refId, hittype) {
                
                var typeCode = type;
                var vm=this;
                if (type == "txtcity") {
                    typeCode = 'STATE_DISTRICT';
                }
                else if (type == "txtstate") {
                    typeCode = 'ACCOUNT_STATE';
                }
                if (refId != '') {                    
                    var url = "id=&moduleName="+vm.$route.params.moduleName+"&type=" + typeCode + "&refId=" + refId;
                    LeadDataService.getDDLData(url).then(function (response) {
                            var json = [];
                            if(response.data=='' && type == 'txtstate'){
                                vm.StateDDLData = [];
                            }
                            if (response.data != null) {
                                json = response.data.DATA;
                            }
                            if (json != null && json != undefined) {
                                if (type == 'txtstate') {
                                    if (hittype != 'EDIT') {                                      
                                        vm.Addrdata.txtstate = '';
                                    }
                                    vm.StateDDLData = json;
                                }
                            }
                        })
                }
                else {
                    if (type == 'txtstate') {
                        vm.Addrdata.txtstate = '';
                        vm.StateDDLData = [];
                    }
                }
            },
            GetCountryValues: function () {
                var vm = this;
                var url = "id=&moduleName="+vm.$route.params.moduleName+"&type=COUNTRIES";
                 
                LeadDataService.getDDLData(url).then(function (response) {
                         
                        if (response.data != null) {
                            vm.countryDDLData = response.data.DATA;
                        }
                        if (vm.Addrdata.useraddrid == 0) {
                            var cntrydata = response.data.DATA;
                            $.each(cntrydata, function (index, item) {
                                if (item.name == "United States")
                                    vm.Addrdata.ddlcountryid = item.value;
                            });
                            vm.FillDropdown('txtstate', vm.Addrdata.ddlcountryid, null);
                        }
                         
                    });
            },
            GetddlContacts: function () {
                var vm = this;
                var url =  "id=&moduleName="+vm.$route.params.moduleName+"&type=CONTACTS_ACCOUNT&refId="+vm.$route.params.id;
                 
                LeadDataService.getDDLData(url).then(function (response) {
                         
                        if (response.data != null) {
                            vm.contactDDLData = response.data.DATA;
                        }
                         
                    });
            },
            SaveUpdateAccountAddress: function (values,meta) {
                //  this.$refs.form.setFieldTouched('AddressName', true); 
                //  this.$refs.form.setFieldTouched('Address1', true); 
                //  this.$refs.form.setFieldValue('Country', String(this.Addrdata.ddlcountryid)); 
                var vm = this;
                vm.$refs.form.validate().then((result) => {                   
                    if (result) {
                        vm.Addrdata.actiontype = 'Save';
                        vm.Addrdata.ddluserid = vm.Addrdata.ddluserid == "" ? null : vm.Addrdata.ddluserid;
                        var objectPostString = JSON.stringify(vm.Addrdata);
                        LeadDataService.SaveAccountAddress(objectPostString).then(function (response) {
                            if (response.data.Status == "SUCCESS") {
                                vm.ShowAlert(vm.$t("Address has been added Successfully."), "success", true, vm.$t("Alert"));
                                vm.$emit('close');
                                vm.$parent.loadData('address');
                            }
                            else if (response.data.Status == "UPDATE") {
                                vm.ShowAlert(vm.$t("Address has been updated Successfully."), "success", true, vm.$t("Alert"));
                                vm.$emit('close');
                                vm.$parent.loadData('address');
                            }
                            else {
                                vm.ShowAlert(vm.$t('ErrorTryAgain'), "failure", true, vm.$t("Alert"));
                            }                             
                        });
                    }
                    else {
                        $('#Fields_mandatory').show();
                    }
                })
            }
        }
}
</script>
<template>
    <transition name="modal">
        <div class="modal d-block my-popups dialog">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <slot name="header">
                            <h5 class="modal-title">{{addressheader}}</h5>
                        </slot>
                        <button v-on:click="$emit('close')" type="button" title="" class="close" data-original-title="Close"><em class="fa fa-times" aria-hidden="true"></em></button>
                    </div>
                    <div class="modal-body">
                        <Form ref="form" v-slot="{values,meta}">
                            <div class="d-flex flex-wrap">
                                <div class="col-md-12 p-0">
                                    <div class="row">
                                        <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                            <div class="form-group">
                                                <label>{{$t('AddressName')}}:<span class="text-danger">*</span></label>
                                                <Field name="AddressName" :rules="{'required':true}" label="Message" v-slot="{errors,touched,invalid}">
                                                <input  v-bind:class="{ 'form-control': true, 'is-invalid': touched && invalid }" v-model="Addrdata.txtaddrname" name="AddressName" type="text" maxlength="100">
                                                <span   name="AddressName" class="invalid-feedback">{{errors[0]}}</span>
                                                </Field>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                            <div class="form-group">
                                                <div class="form-group">
                                                    <label>Site Contact:</label>                                                    
                                                    <select class="form-control" v-model="Addrdata.ddluserid" name="ContactName">
                                                        <option value="">Select</option>
                                                        <option v-for="(item,itemIndex) in contactDDLData" :key="itemIndex" v-bind:value="item.value">{{item.name}}</option>
                                                    </select>                                                   
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                            <div class="form-group">
                                                <label>{{$t('Address1')}}:<span class="text-danger">*</span></label>
                                                <Field name="Address1" :rules="{'required':true}" label="Address1" v-slot="{errors,touched,invalid}">
                                                <input  v-bind:class="{ 'form-control': true, 'is-invalid': touched && invalid }"  v-model="Addrdata.txtaddr1" name="Address1" type="text" maxlength="200">
                                                <span   name="Address1" class="invalid-feedback">{{errors[0]}}</span>
                                                </Field>

                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                            <div class="form-group">
                                                <label>{{$t('Address2')}}:</label>                                               
                                                <input v-bind:class="{'form-control':true}" v-model="Addrdata.txtaddr2" name="Address2" type="text" maxlength="200">                                               
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                            <div class="form-group">
                                                <label>{{$t('City')}}:</label>
                                                <input v-bind:class="{'form-control':true}" v-model="Addrdata.txtcity" name="City" maxlength="200" type="text">                                              
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                            <div class="form-group">
                                                <label>{{$t('State')}}:</label>   
                                                <select v-bind:class="{'form-control':true}" v-model="Addrdata.txtstate" name="State" v-on:change="OnChangeDropdown($event,'txtcity')">
                                                    <option value="">Select</option>
                                                    <option v-for="(item,itemIndex1) in StateDDLData" :key="itemIndex1" v-bind:value="item.value">{{item.name}}</option>
                                                </select>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                            <div class="form-group">
                                                <label>{{$t('ZipCode')}}:<span class="text-danger"></span></label>
                                                <Field name="ZipCode" :rules="{'ZipCode':true}"   v-slot="{errors}">        
                                                <input v-bind:class="{'form-control':true, 'is-invalid': errors[0]}" v-model="Addrdata.txtzipcode"  name="ZipCode" maxlength="15" type="text">
                                                <span   name="ZipCode" class="invalid-feedback">{{errors[0]}}</span>   </Field>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                            <div class="form-group">
                                                <label>{{$t('Country')}}:<span class="text-danger">*</span></label>
                                                <Field name="Country" :rules="{'required':true}"   v-slot="{errors}">         
                                                <select v-model="Addrdata.ddlcountryid"  v-bind:class="{ 'form-control': true, 'is-invalid': errors[0]}" name="Country"  v-on:change="OnChangeDropdown($event,'txtstate')">
                                                    <option value="">Select</option>
                                                    <option v-for="(item,itemIndex2) in countryDDLData" :key="itemIndex2" v-bind:value="item.value">{{item.name}}</option>
                                                </select>
                                                <span   v-if="errors[0]" class="invalid-feedback">{{errors[0]}}</span>   </Field>
                                               
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                            <div class="form-group">
                                                <label>{{$t('Description')}}:<span class="text-danger"></span></label>
                                                <input name="txtdescription" v-bind:class="{'form-control':true}" v-model="Addrdata.txtdescription" maxlength="200" type="text">
                                            </div>
                                        </div>                                     
                                        
                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                            <div class="row flex-row-reverse">
                                                <div class="col-lg-6 text-right">
                                                    <a href="javascript:;" class="btn btn-success formbtn text-white" v-on:click="SaveUpdateAccountAddress(values,meta)"><em class="fa fa-save pr-2"></em>{{$t('Save')}}</a>
                                                    &nbsp;&nbsp;
                                                    <a v-on:click="$emit('close')" class="btn btn-danger formbtn widthhalf text-white"><em class="fa fa-close pr-2"></em>{{$t('Cancel')}}</a>
                                                </div>
                                                <div class="col-lg-6">
                                                    <div class="py-2">
                                                       
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>