<script>
import LeadDataService from "../../../services/DataService";
export default {
    props: {
        contactsdata: {
            type: Array,
            required: true
        },
        contactsheader: {
            type: Array,
            required: true
        },
        companyid: {
            type: String,
            required: true
        },

    },
    data: function () {
        return {
            ContactsData: [],
            noRecordForTab: true,
            ContactsHeader: [],
            ItemData: [],
            RoleList: [],
            AccType: '',
            userType: [],
            ResetCount: 0,
            OldModuleID: null,
            OldUserType: '',
            OldUserRole: '',
            IsLoginSlider: false,
            LicenseData:null
        }
    },
    watch: {
        contactsheader: function (value) {
            this.ContactsHeader = value;
        },
        contactsdata: function (value) {
            var vm = this;
            vm.ContactsData = value;
            vm.ItemData = value[0];
            const urlParams = new URLSearchParams(window.location.search);
            vm.AccType = urlParams.get('accountcode');
            if (vm.AccType != "NEW_CUSTOMER") {
                vm.ItemData.userType = vm.ItemData.userType == 4 ? "" : (vm.ItemData.userType == 0 ? "" : vm.ItemData.userType);
            }
            else {
                vm.ItemData.userType = 4;
            }
            var userid = vm.ItemData.USER_ID;
            var usertype = vm.ItemData.userType == 0 ? 4 : vm.ItemData.userType;
            vm.OldUserType = vm.ItemData.userType;
            vm.OldUserRole = vm.ItemData.RoleID;
            if ((vm.ItemData.userType != 0 && vm.ItemData.userType != "") && vm.AccType != "NEW_CUSTOMER") {                
                var url = "userType=" + (usertype == '' ? 0 : usertype);
                    LeadDataService.GetAssociateLicense(url).then(function (response) {
                        vm.LicenseData = response.data;
                    });
                // $("#divLicencedata").load("/User/openAssociateLicensePopup?companyId=" + vm.companyid + "&userID=" + userid + "&userType=" + usertype, function (data) {
                //     vm.OldModuleID = '';
                //     $("#divAssignedLicence .aRemoveLicense").each(function (index, licencedata) {
                //         if (licencedata.attributes.moduleid.value != "")
                //             vm.OldModuleID = vm.OldModuleID + licencedata.attributes.moduleid.value + ',';
                //     });
                //     vm.OldModuleID = vm.OldModuleID.substring(0, vm.OldModuleID.length - 1);
                // });
            }
            vm.FetchRoleList(vm.ItemData.userType);
            vm.noRecordForTab = ((vm.ContactsData != null && vm.ContactsData.length > 0) ? false : true);
            setTimeout(function () { vm.ResponsiveDataTable('table-contacts-content') }, 500);
        }
    },
    created: function () {
        const urlParams = new URLSearchParams(window.location.search);
        this.AccType = urlParams.get('accountcode');
        this.userType = [];
        if (this.AccType == "NEW_CUSTOMER") {
            this.userType[0] =
            {
                name: "Client",
                typeid: 4
            }
        }
        else {
            this.userType = [
                {
                    name: "Normal User",
                    typeid: 3
                },
                {
                    name: "Project Manager",
                    typeid: 2
                }
            ]
        }
    },
    methods: {
        FetchRoleList: function (usertype) {
            var vm = this;
            var url = "userType=" + (usertype == '' ? 0 : usertype);
            LeadDataService.UserRoleList(url).then(function (response) {
                var data = response.data.DATA;
                vm.RoleList = data;
            });
        },
        SetLoginMouseDown: function () {
            vm.IsLoginSlider = true;
        },
        SetLoginOrPrimary: function (itm, event) {
            debugger;
            var vm = this;
            if (!vm.IsLoginSlider) {
                vm.IsLoginSlider = true;
                var message = vm.$t("MessageLogin");
                if (!itm.IsLogin && vm.AccType != "NEW_CUSTOMER")
                    message = vm.$t("MessageLoginDisabled");
            }
            vm.confirmR(message, true, null, function (result) {
                var isFalse = false;
                vm.IsLoginSlider = false;
                if (itm.IsPrimary) {
                    $.each(vm.ContactsData, function (index, item) {
                        if (item.contact_id != itm.contact_id) {
                            item.IsPrimary = false;
                            item.isDisable = false;
                        }
                        else {
                            if (item.USER_ID != null && item.USER_ID != "" && !item.IsLogin) {
                                item.IsLogin = true;
                                item.isDisable = true;
                                vm.ShowAlert("This is a primary contact. You can't disable login.", "warning", true, vm.$t("Alert"));
                                isFalse = true;
                                return false;
                            }
                        }
                    });
                }
                else if (!itm.IsPrimary)
                    itm.isDisable = false;
                if (!isFalse) {
                    vm.ResetCount++;
                    vm.$parent.SaveAccount(itm, "direct");
                    setTimeout(function () {
                        itm.userType = "";
                        itm.RoleList = null;
                        itm.RoleID = "0";
                        vm.ItemData = itm;
                    }, 500);
                    if (!itm.IsLogin && vm.AccType != "NEW_CUSTOMER") {
                        var moduleID = $.trim($("#divAssignedLicence .aRemoveLicense").attr("moduleid"));
                        if (moduleID != "") {
                        }
                    }
                }
                else {
                    itm.IsLogin = !itm.IsLogin;
                    vm.IsLoginSlider = false;
                }
            });
        },
        Save: function (item) {
            var vm = this;
            var islogin = item.IsLogin;
            var licenceVal = $.trim($("#dvAssociatedLicensesPOP").val());
            if (vm.AccType != "NEW_CUSTOMER") {

                if (vm.ItemData.length == 0) {
                    item = vm.ContactsData[0];
                }
                if (!islogin) {
                    vm.$parent.SaveAccount(item);
                    vm.$emit('close');
                }
                else if (islogin || licenceVal != "") {
                    vm.$parent.SaveAccount(item);
                    vm.$emit('close');
                }
                else {
                    vm.ShowAlert("You have to select at least one license.", "warning", true, vm.$t("Alert"));
                }
            }
            else {
                vm.$parent.SaveAccount(item);
                vm.$emit('close');
            }

        },
        changeLicence: function (usertype, userid, item) {
            var vm = this;
            userid = userid == null ? -2 : userid;
            item.RoleID = "0";
            //$("#divLicencedata").html("");
            vm.FetchRoleList(usertype);
            if (usertype != 0) {
                //$(".managefolder").html("");
                var url = "userType=" + (usertype == '' ? 0 : usertype);
                    LeadDataService.GetAssociateLicense(url).then(function (response) {
                        vm.LicenseData = response.data;
                        setTimeout(function () { $(".ancuserguide").hide() }, 360);
                    });
            }
        },
        CloseLogin:function(item) {
            var vm = this;
            var cnt = vm.ResetCount;
            var mode = cnt % 2;
            var OldModuleIds = "";
            if (vm.OldModuleID != null)
                OldModuleIds = vm.OldModuleID.split(',');
            var moduleID = $.trim($("#divAssignedLicence .aRemoveLicense").attr("moduleid"));
            if (mode == 1) {
                item.IsLogin = !item.IsLogin;
            }
            item.RoleID = vm.OldUserRole;
            item.userType = vm.OldUserType;
            if (moduleID != "") {
            }
            else if (OldModuleIds.length > 0 && moduleID == "" && (item.USER_ID != null && item.USER_ID != "")) {
                $.each(OldModuleIds, function (index, oldMdlid) {
                });
            }
            if (item.USER_ID != null && item.USER_ID != "" && cnt > 0)
                vm.$parent.SaveAccount(item, "direct");
            vm.$emit("close");

        },
        AssociateLicenses:function(item){
            var vm=this;
            var url= "moduleMasterID="+item.Module_Master_Id+"&actionType=Associate&IsRemovedAll="+false;
            LeadDataService.AssociateDeAssociateLicense(url).then(function (response) {
                vm.LicenseData = response.data;
            });
        },
        DeAssociateLicenses:function(item){
            var vm=this;
            var url= "moduleMasterID="+item.MODULE_ID+"&actionType=De-associate&IsRemovedAll="+false;
            LeadDataService.AssociateDeAssociateLicense(url).then(function (response) {
                vm.LicenseData = response.data;
            });
        }
    }
}

</script>
<template>
    <div class="modal d-block my-popups dialog">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <slot name="header">
                        <h5 class="modal-title">login</h5>
                    </slot>
                    <button @click="CloseLogin(ItemData)" type="button" class="close"><em class="fa fa-times"
                            aria-hidden="true"></em></button>

                </div>
                <div class="modal-body">
                    <form data-vv-scope="Login">
                        <div class="d-flex flex-wrap">
                            <div class="col-md-12 p-0">
                                <div class="row">
                                    <div class="col-md-4 col-lg-4">
                                        <div class="form-group">
                                            <label>
                                                <strong>Name: </strong>
                                                <span class="rightlabeltext">{{ ItemData.first_name }}
                                                    {{ ItemData.last_name }}</span>
                                            </label>
                                        </div>
                                    </div>
                                    <div class="col-md-4 col-lg-4">
                                        <div class="form-group">
                                            <label>
                                                <strong>Email: </strong>
                                                <span class="rightlabeltext">{{ ItemData.email_id }}</span>
                                            </label>
                                        </div>
                                    </div>
                                    <div class="col-md-4 col-lg-4">
                                        <label class="align-top">
                                            <strong>Enable Login : </strong>
                                        </label>
                                        <div class="form-group d-inline-block">
                                            <label class="switch">
                                                <input class="form-check-input custom-control-input dynamic"
                                                    v-bind:disabled="IsLoginSlider" v-on:mousedown="SetLoginMouseDown"
                                                    v-model="ItemData.IsLogin"
                                                    v-on:change="SetLoginOrPrimary(ItemData, $event)" type="checkbox"
                                                    v-bind:name="'IsLogin'">
                                                <span class="slider round ml-1">
                                                    <span class="slider-yes">Yes</span>
                                                    <span class="slider-no">No</span>
                                                </span>
                                            </label>
                                        </div>
                                    </div>

                                    <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left" v-if="AccType !='NEW_CUSTOMER' && ItemData.IsLogin">
                                        <div class="form-group">
                                        <Field name="User" v-model="ItemData.userType" label="User Type"  v-slot="{errors,touched,invalid}">
                                            <label><strong>User Type:</strong><span class="text-danger">*</span></label>
                                            <select v-bind:class="{'form-control':true, 'is-invalid':touched && invalid}" 
                                                v-on:change="changeLicence(ItemData.userType,ItemData.USER_ID,ItemData)" 
                                                v-bind:disabled="AccType =='NEW_CUSTOMER'">
                                                <option value="" selected>{{$t('Select')}}</option> 
                                                <option v-for="user in userType" v-bind:value="user.typeid">{{ user.name }}</option>
                                            </select>
                                            <span   name="User" class="invalid-feedback">{{errors[0]}}</span>
                                        </Field>
                                        </div> 
                                    </div> 

                                    <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left" v-if="ItemData.IsLogin && AccType !='NEW_CUSTOMER'">
                                        <div class="form-group">
                                            <Field name="role" label="Role" v-model="ItemData.RoleID">
                                                <label><strong>Role:</strong></label>
                                                <select class="form-control">
                                                    <option value="0" selected>{{$t('Select')}}</option>
                                                    <option v-for="role in RoleList" v-bind:value="role.role_id">{{ role.role_name }}</option>
                                                </select>
                                            </Field>
                                        </div>
                                    </div>
                                </div>
                                    
                                <div class="row" v-show="ItemData.IsLogin" v-if="AccType !='NEW_CUSTOMER'">
                                        <div id="divLicencedata" style="width:100%">
                                            <div class="col-sm-12 col-md-12 col-lg-12 form-panel mt-0 p-0">
                                            <div class="form-content">
                                                <form>
                                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left" id="divtickettupelist">
                                                        <div class="heading-border">
                                                            <h5 class="h5"><span>{{ $t('allLicenses') }}</span></h5>
                                                        </div>
                                                        <div class="managefolder py-3 px-2">
                                                            <ul class="list-group" style="max-height: 400px;min-height: 200px;overflow-y: auto;">
                                                                <li class="list-group-item single-action" v-if="LicenseData !=null" v-for="item in LicenseData.allLicenses">
                                                                console.log(LicenseData.allLicenses)
                                                                    {{item.MODULE_NAME}} ({{item.LICENSE_COUNT}})
                                                                    <span v-if="item.EXISTSFORUSER <=0">
                                                                        <a v-if="item.LICENSE_COUNT > 0" class="aAddLicense float-right" href="javascript:;" :moduleid="item.Module_Master_Id" @click="AssociateLicenses(item)">
                                                                            <em class="fa fa-plus-circle text-success action-icon" alt="Click to Associate License" title="Click to Associate License"></em>
                                                                        </a>
                                                                        <a v-else class="aAddLicense float-right disabled" href="javascript:;">
                                                                            <em class="fa fa-plus-circle text-success action-icon disabled" alt="License not Available" title="License not Available"></em>
                                                                        </a>
                                                                    </span>
                                                                    <span v-else>
                                                                        <a class="aAddLicense float-right disabled" href="javascript:;">
                                                                            <em class="fa fa-plus-circle text-success action-icon disabled" alt="Already Associated" title="Already Associated"></em>
                                                                        </a>
                                                                    </span>
                                                                </li>
                                                                <li v-else>
                                                                    <li class="list-group-item list_username list_img"><strong>{{ $t('LicenseNotAvailable') }}</strong></li>
                                                                </li>
                                                            </ul>
                                                        </div>
                                                    </div>
                                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left" id="divAssignedLicence">
                                                        <div class="heading-border">
                                                            <h5 class="h5"><span>{{ $t('addedLicenses') }}</span></h5>
                                                        </div>
                                                        <div class="managefolder py-3 px-2">
                                                            <ul class="list-group" style="max-height: 400px;min-height: 200px;overflow-y: auto;">
                                                                <li v-if="LicenseData !=null" v-for="item in LicenseData.addedLicenses" class="list-group-item list_username list_img single-action" data-typename="EMER NEW GRID DGIN">
                                                                    {{ item.MODULE_NAME }}
                                                                    <a class="aRemoveLicense float-right" href="javascript:void(0)" :moduleid="item.MODULE_ID" @click="DeAssociateLicenses(item)">
                                                                        <em class="fa fa-times-circle text-danger action-icon" alt="Click to Deassociate License" title="Click to Deassociate License"></em>
                                                                    </a>
                                                                </li>
                                                                <li v-else>
                                                                    <li class="list-group-item list_username list_img"><strong>{{ $t('LicenseNotAvailable') }}</strong></li>
                                                                </li>
                                                            </ul>
                                                        </div>

                                                    </div>
                                                    
                                                </form>
                                            </div>
                                        </div>
                                        </div>
                                    </div>

                                    <div class="col-12">
                                        <div class="row flex-row-reverse">
                                            <div class="col-lg-6 text-right">
                                                <a href="javascript:;" class="btn btn-success text-white" @click="Save(ItemData)" id="btnSave" title="">
                                                    <em class="fa fa-save pr-2"></em>{{ $t('Save') }}
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>